import { useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import NftGalleryCard from '@components/Card/NftGalleryCard'
import '@assets/css/pages/NftGallery.css'
import { Grid } from '@mui/material'
import AddCircleOutlinedIcon from '@mui/icons-material/AddCircleOutlined'
import { useDispatch, useSelector } from 'react-redux'
import NftGalleryDetail from './NftGalleryDetail'
import { RootState } from '@root/store/rootReducers'
import {
  fetchGalleryData,
  showGalleryDetail,
  updateNFT,
} from '@root/apis/gallery/gallerySlice'
import classNames from 'classnames'
import toast from 'react-hot-toast'
import HotToaster from '@components/HotToaster'
import { isMobile } from '@utils/helpers'

interface Props {
  isLaunch?: boolean
  onSetNftImg?: (img: string) => any
}
const NftGalleryForm: React.FC<Props> = ({ isLaunch, onSetNftImg }) => {
  const { t } = useTranslation()
  const inputFile = useRef<HTMLInputElement>(null)
  const dispatch = useDispatch()
  const [item, setItem] = useState(null)
  const [showDetail, setShowDetail] = useState(false)

  const {
    isGalleryLoading,
    galleryData,
    isUpdateNFTSuccess,
    isUpdateNFTFailure,
  } = useSelector((state: RootState) => state.gallery)

  const isGalleryDetailVisible = useSelector(
    (state: RootState) => state.gallery.isGalleryDetailVisible,
  )

  const { allPlayersData } = useSelector(
    (state: RootState) => state.playercoins,
  )

  localStorage.setItem('playerIndex', '0')
  const playerIndex = parseInt(localStorage.getItem('playerIndex') ?? '0')

  useEffect(() => {
    dispatch(fetchGalleryData())
  }, [])

  useEffect(() => {
    if (isUpdateNFTSuccess) {
      dispatch(fetchGalleryData())
    }
    if (isUpdateNFTFailure) {
      // toast.error(t('unknown exception'))
    }
  }, [isUpdateNFTSuccess, isUpdateNFTFailure])

  const handleUpload = () => {
    if (inputFile.current) {
      inputFile.current.click()
    }
  }

  console.log('galleryData', galleryData)

  const onUpload = (e: any) => {
    e.preventDefault()
    const files = e.target.files
    const reader = new FileReader()
    reader.readAsDataURL(files[0])
    if (files && files.length) {
      const formData = new FormData()
      formData.append('image', files[0])
      formData.append('playerid', allPlayersData[playerIndex].id)
      dispatch(updateNFT(formData))
    }
  }

  const handleClick = (item: any) => {
    if (isLaunch) {
      handleLaunch(item)
    } else {
      console.log('item', item)
      setItem(item)
      setShowDetail(true)
      dispatch(showGalleryDetail(true))
    }
  }

  const handleLaunch = (item: any) => {
    onSetNftImg && onSetNftImg(item.artwork ? item.artwork : item.artwork_thumb)
  }

  return (
    <>
      <HotToaster />
      {!isGalleryDetailVisible || !showDetail || isLaunch ? (
        <div
          className={classNames(
            'nft-gallery',
            isGalleryLoading ? 'align-center' : '',
          )}
        >
          {isGalleryLoading ? (
            <div
              className={classNames(
                'loading-spinner-container mb-40 mt-40',
                isGalleryLoading ? 'show' : '',
              )}
            >
              <div className="loading-spinner">
                <div className="spinner__circle">
                  <div className="spinner__circle-gradient"></div>
                  <div className="spinner__circle-inner"></div>
                </div>
              </div>
            </div>
          ) : (
            <>
              {!isMobile() && (
                <div className="nft-gallery-title h-2">
                  {t('my NFT Gallery')}
                </div>
              )}
              {!galleryData || galleryData.length < 1 ? (
                <div className="nft-tab-title yellow-color">
                  {t('no NFTs uploaded')}
                </div>
              ) : (
                <div className="nft-gallery-grid">
                  <Grid container>
                    {galleryData &&
                      galleryData.map((item: any, index: any) => {
                        return (
                          <Grid
                            item
                            md={6}
                            xs={6}
                            className={
                              index % 2 === 0
                                ? 'nft-gallery-leftline'
                                : 'nft-gallery-rightline'
                            }
                            key={index}
                          >
                            <NftGalleryCard
                              img={
                                item.artwork_thumb
                                  ? item.artwork_thumb
                                  : item.artwork
                              }
                              status={item.statusid.id}
                              onClick={() => handleClick(item)}
                              onLaunch={() => handleLaunch(item)}
                            />
                          </Grid>
                        )
                      })}
                  </Grid>
                </div>
              )}
              <div className="plus-icon" onClick={handleUpload}>
                <AddCircleOutlinedIcon />
                <input
                  id="media"
                  name="nftMedia"
                  accept="image/*"
                  type="file"
                  autoComplete="off"
                  tabIndex={-1}
                  style={{ display: 'none' }}
                  ref={inputFile}
                  onChange={onUpload}
                />
              </div>
            </>
          )}
        </div>
      ) : (
        <NftGalleryDetail
          item={item}
          onBack={() => dispatch(showGalleryDetail(false))}
          onLaunch={handleLaunch}
        />
      )}
    </>
  )
}

export default NftGalleryForm
