export const convertToFixed = (exp: any) => {
  if (Math.abs(exp) < 1.0) {
    const e = parseInt(exp.toString().split('e-')[1])
    if (e) {
      exp *= Math.pow(10, e - 1)
      exp = '0.' + new Array(e).join('0') + exp.toString().substring(2)
    }
  } else {
    let e = parseInt(exp.toString().split('+')[1])
    if (e > 20) {
      e -= 20
      exp /= Math.pow(10, e)
      exp += new Array(e + 1).join('0')
    }
  }
  return truncateDecimals(parseInt(exp), 2)
}

const isValid = (
  methodName: string,
  player1contract: string,
  player1contractabi: any,
) => {
  let validTest = {}

  if (!methodName) {
    validTest = { ...validTest, isError: t('method name is required') }
  } else if (!player1contract) {
    validTest = {
      ...validTest,
      isError: t('host contract address is required'),
    }
  } else if (!player1contractabi) {
    validTest = { ...validTest, isError: t('host contract abi is required') }
  }
  return validTest
}

interface validationInstanceObj {
  isError?: string
}

export const callWeb3Method = (
  methodName: string,
  player1contract: string,
  player1contractabi: any,
  inputPrams: string[] | any, // 2nd player contract adddress
) => {
  console.log({
    methodName,
    player1contract,
    player1contractabi,
    inputPrams,
  })
  const isValidInstace: validationInstanceObj = isValid(
    methodName,
    player1contract,
    player1contractabi,
  )
  // const provider = new ethers.providers.Web3Provider(window.ethereum)
  let walletProvider = window.ethereum?.providers?.find((provider: any) =>
    localStorage.getItem('wallet') === 'Metamask'
      ? provider.isMetaMask
      : localStorage.getItem('wallet') === 'Coinbase'
      ? provider.isCoinbaseWallet
      : provider.isTrustWallet,
  )
  if (!walletProvider) {
    if (
      (window.ethereum?.isMetaMask &&
        localStorage.getItem('wallet') === 'Metamask') ||
      (window.ethereum?.isCoinbaseWallet &&
        localStorage.getItem('wallet') === 'Coinbase')
    ) {
      walletProvider = window.ethereum
    }
  }
  const provider = new ethers.providers.Web3Provider(walletProvider)
  console.log('cooolpro--', { provider })
  const web3Promise = new Promise(function (resolve, reject) {
    try {
      const loginWallet = localStorage.getItem('loginInfo')
      if (loginWallet) {
        if (!isValidInstace?.isError) {
          const playerContract = new ethers.Contract(
            player1contract,
            player1contractabi,
            provider.getSigner(loginInfo!),
          )
          console.log({ playerContract })
          let tx = null
          if (inputPrams.length > 0) {
            tx = playerContract[methodName](inputPrams.join(), {
              gasLimit: 500000,
            })
          } else {
            tx = playerContract[methodName]()
          }
          resolve(tx)
        } else {
          throw new Error(isValidInstace?.isError)
        }
      } else {
        throw new Error('406')
      }
    } catch (e: any) {
      console.log('WEB3ERR---', e)
      reject(e)
    }
  })
  return web3Promise
}
const pad = (num: any) => ('0' + num).slice(-2) // or use padStart
export const getTimeFromDate = (timestamp: any) => {
  const date = new Date(timestamp * 1000)
  const hours = date.getHours(),
    minutes = date.getMinutes(),
    seconds = date.getSeconds()
  return pad(hours) + ':' + pad(minutes) + ':' + pad(seconds)
}

export function toFixedTrunc(x: any, n: number) {
  const v = (typeof x === 'string' ? x : x.toString()).split('.')
  if (n <= 0) return v[0]
  let f = v[1] || ''
  if (f.length > n) return `${v[0]}.${f.substr(0, n)}`
  while (f.length < n) f += '0'
  return `${v[0]}.${f}`
}

export const getCountryName = (iso: string) => {
  if (iso) {
    const countryIndex = countriesData.findIndex(
      country => country.iso === iso.toUpperCase(),
    )
    return countriesData[countryIndex].country
  }
  return ' '
}
