import { useEffect, useState } from 'react'
import ArrowDown from '@components/Svg/ArrowDown'
import { useTranslation } from 'react-i18next'
import SubmitButton from '@components/SubmitButton'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '@root/store/rootReducers'
import classNames from 'classnames'
import { getVoteList } from '@root/apis/playerCoins/playerCoinsSlice'

interface Props {
  onCreate: () => any
}
const PollList: React.FC<Props> = ({ onCreate }) => {
  const dispatch = useDispatch()
  const { t } = useTranslation()
  const [selectedPoll, setSelectedPoll] = useState(-1)

  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const { isLoading, isGetVoteListSuccess, isGetVoteListError, voteList } =
    playerCoinData

  const handleClick = (e: number) => {
    setSelectedPoll(e)
  }

  useEffect(() => {
    dispatch(getVoteList())
  }, [])

  return (
    <>
      <div className="nft-tab-title">{t('create a new voting poll')}</div>
      <SubmitButton
        title={t('create voting poll')}
        className="nft-create-btn"
        onPress={onCreate}
      />
      {isLoading ? (
        <div
          className={classNames(
            'loading-spinner-container mb-40 mt-40',
            isLoading ? 'show' : '',
          )}
        >
          <div className="loading-spinner">
            <div className="spinner__circle">
              <div className="spinner__circle-gradient"></div>
              <div className="spinner__circle-inner"></div>
            </div>
          </div>
        </div>
      ) : (
        <div className="fullwidth NftList-poll-root">
          {voteList.map((item: any, index: number) => (
            <div
              className="NftList-poll-container NftList-poll-closed-container"
              onClick={() => handleClick(index)}
              key={index}
            >
              <div className="NftList-poll-header">{item.question}</div>
              {selectedPoll === index && (
                <div className="NftList-poll-body">
                  <div className="NftList-poll-item">
                    <div>Verde Mantis({t('green')})</div>
                    <div>52%</div>
                  </div>
                  <div className="NftList-poll-item">
                    <div>Rossa Mars({t('red')})</div>
                    <div>42%</div>
                  </div>
                  <div className="NftList-poll-item">
                    <div>Bianco Isi({t('white')})</div>
                    <div>38%</div>
                  </div>
                  <div className="NftList-poll-item">
                    <div>Giallo Orion({t('yellow')})</div>
                    <div>12%</div>
                  </div>
                  <div className="NftList-poll-item">
                    <div>Nero Aldebaran({t('black')})</div>
                    <div>28%</div>
                  </div>
                </div>
              )}
              <div className="NftList-poll-footer">
                <div>{t('ends')} June 8, 11:57 PM</div>
                <div>
                  <span>10K {t('votes')}</span>
                  <span className="arrow-down">
                    <ArrowDown />
                  </span>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </>
  )
}

export default PollList
