import React, { useEffect, useState } from 'react'
import AddIcon from '@mui/icons-material/Add'
import { PlayerCardData as PlayerItems } from '@root/constants'
import SearchBar from '@components/SearchBar'
import { NumberFormat } from '@utils/NumberFormat'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '@root/store/rootReducers'
import {
  fetchDraftNewPlayers,
  getPlayer1Contract,
  getPlayer2Contract,
  getPlayerData,
  persistDrafteePlayer,
  resetDraftNewPlayers,
} from '@root/apis/playerCoins/playerCoinsSlice'
import toast from 'react-hot-toast'
import { ethers } from 'ethers'
import ResponseAlert from '@components/ResponseAlert'
import classNames from 'classnames'
import InfiniteScroll from 'react-infinite-scroll-component'
import { callWeb3Method, isMobile } from '@utils/helpers'
import BottomPopup from '@components/BottomPopup'
import { useNavigate } from 'react-router'
import SubmitButton from '@components/SubmitButton'
import MetamaskIcon from '@assets/icons/icon/metamask.svg'
import CoinbaseIcon from '@assets/icons/icon/coinbase.svg'
import CheckCircleOutlinedIcon from '@mui/icons-material/CheckCircleOutlined'
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined'
interface Props {
  onClose: (value?: any) => void
}
interface DraftFiltersData {
  limit?: string
  offset?: string
  sorted_by?: string
  q?: string
  reverse?: string
  status_id?: number[]
}

const style = {
  height: 30,
  border: '1px solid green',
  margin: 6,
  padding: 8,
}

const NewDraft: React.FC<Props> = ({ onClose }) => {
  const { t } = useTranslation()
  const walletType = localStorage.getItem('wallet')
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [txnHash, setTxnHash] = useState('')
  const [allPlayers, setAllPlayers] = useState<any>([])
  const [searchFocussed, setSearchFocussed] = useState<any>(false)
  const [searchedTerm, setSearchedTerm] = useState<string | undefined>('')
  const [isLoading, setLoading] = useState(false)
  const [isDeadEnd, setIsDeadEnd] = useState(false)
  const [targetPlayer, setTargetPlayer] = useState('')
  const [noDataFound, setNoDataFound] = useState(false)
  const [endLimit, setEndLimit] = useState(false)
  const [txnError, setTxnError] = useState('')
  const [appliedFilters, setAppliedFilters] = useState<DraftFiltersData>({
    sorted_by: 'market_cap',
    reverse: 'True',
    status_id: [3, 4],
    limit: 10,
    offset: 0,
  })
  const [testItems, setTestItems] = useState(Array.from({ length: 20 }))
  const [hasMore, setHasMore] = useState(true)

  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const {
    fetchDraftPlayersLoading,
    deployedPlayersList,
    isDraftPlayersLoading,
    totalDeployedPlayersCount,
    nextDeployedPlayerListUrl,
    previousDeployedPlayerListUrl,
    isFetchDeployedPlayerSuccess,
    isLoadingPlayerList,
    isGetPlayerError,
  } = playerCoinData

  const {
    allPlayersData,
    playercontract,
    playercontractabi,
    isGetPlayerContractSuccess,
    isGetPlayerContractError,
    isGetPlayer1ContractSuccess,
    isGetPlayer2ContractSuccess,
    isGetPlayer1ContractError,
    isGetPlayer2ContractError,
    player2contract,
    player1contract,
    player2contractabi,
    player1contractabi,
  } = useSelector((state: RootState) => state.playercoins)
  const loginId = localStorage.getItem('loginId')
  const loginInfo = localStorage.getItem('loginInfo')

  const handleDraftPlayer = async (playerItem: any) => {
    const { detailpageurl, name } = playerItem
    // setIsLoadingMatic(true)
    // setIsDisabledMethod(true)
    // setTxHash('')
    // dispatch(getPlayer1Contract(allPlayersData[0]?.detailpageurl))

    //------------------------- 2_Aug_222 -------------------------
    await setSearchFocussed(false)
    // setLoading(true)
    setTargetPlayer(name)
    dispatch(getPlayer2Contract(detailpageurl))
    // if (isMobile()) {
    //   await dispatch(persistDrafteePlayer({ playerUrl }))
    //   navigate(-1)
    // } else {
    //   onClose({ playerUrl })
    // }
  }

  useEffect(() => {
    console.log({ player1contract, player1contractabi })
    if (!player1contract || !player1contractabi) {
      console.log('noabi---', allPlayersData)
      dispatch(getPlayerData())
      // dispatch(getPlayer1Contract(playerInfo?.detailpageurl))
    } else {
      console.log({ player1contract, player1contractabi })
    }
  }, [player1contract, player1contractabi])

  useEffect(() => {
    // if (allPlayersData && allPlayersData.length > 0 && !playerInfo) {
    //   // remove destructuring after api working successfully
    //   const playerDataNew = {
    //     ...allPlayersData[0],
    //     // playerContract: '1abcdncmvjhjdfhjwehjruwer',
    //     // NFTContract: '478abcdncmvjhjdfhjwehjruwer',
    //     // stakingContract: '33abcdncmvjhjdfhjwehjruwer',
    //   }
    //   setPlayerInfo(playerDataNew)
    //   const reqParams = {
    //     address: playerDataNew?.nftcontract,
    //   }
    //   dispatch(fetchPlayersBalance(reqParams))
    // }
    // console.log({ playerInfo })
    // if (playerInfo?.detailpageurl) {
    //   dispatch(getPlayer1Contract(playerInfo?.detailpageurl))
    // }
    if (!player1contract || !player1contractabi) {
      dispatch(getPlayer1Contract(allPlayersData[0]?.detailpageurl))
    }
    console.log({ allPlayersData })
  }, [allPlayersData])

  useEffect(() => {
    // setLoading(true)
    if (appliedFilters?.limit || appliedFilters?.offset) {
      dispatch(fetchDraftNewPlayers(appliedFilters))
    }
    console.log('kkppp---', { appliedFilters })
    // if (appliedFilters?.q) {
    //   setMobileFixedHeightTable(true)
    // } else {
    //   setMobileFixedHeightTable(false)
    // }
  }, [appliedFilters])
  // useEffect(() => {
  //   console.log({ appliedFilters })
  //   setLoading(true)
  //   if (
  //     appliedFilters?.limit ||
  //     appliedFilters?.offset ||
  //     appliedFilters?.sorted_by ||
  //     appliedFilters?.q
  //   ) {
  //     console.log('PRTEST11')
  //     dispatch(fetchListPlayers(appliedFilters))
  //   }
  //   if (appliedFilters?.q) {
  //     setMobileFixedHeightTable(true)
  //   } else {
  //     setMobileFixedHeightTable(false)
  //   }
  // }, [appliedFilters])

  // useEffect(() => {
  //   if (isGetPlayer2ContractSuccess) {
  //     // if (loginInfo) {
  //     buyforExternalWallet()
  //     // }
  //     // else if (loginId) {
  //     //   buyforInternalWallet()
  //     // }
  //   }
  //   if (isGetPlayerContractError) {
  //     toast.error(t('Loading player contract is failed.'))
  //   }
  //   // setIsLoadingMatic(false)
  //   // setIsDisabledMethod(false)
  // }, [isGetPlayer2ContractSuccess, isGetPlayerContractError])

  useEffect(() => {
    // if (deployedPlayersList && deployedPlayersList.length > 0) {
    //   setLoadingPlayers(false)
    // }
    if (appliedFilters.limit || appliedFilters.offset) {
      console.log('44L--')
      if (appliedFilters.q) {
        // const params = new URLSearchParams(nextDeployedPlayerListUrl)
        const url_string = nextDeployedPlayerListUrl
        const url = new URL(url_string)
        const obj = {
          limit: url.searchParams.get('limit'),
          offset: url.searchParams.get('offset'),
        }
        console.log('jss---', { obj, appliedFilters })
        if (appliedFilters.offset === obj.offset) {
          setIsDeadEnd(true)
          setAllPlayers(deployedPlayersList)
        } else {
          setAllPlayers([...allPlayers, ...deployedPlayersList])
        }
      } else {
        console.log('22L--', {
          deployedPlayersList,
          isFetchDeployedPlayerSuccess,
        })
        if (deployedPlayersList.length > 0 && isFetchDeployedPlayerSuccess) {
          console.log('777L--')
          setAllPlayers([...allPlayers, ...deployedPlayersList])
          // setAllPlayers([...allPlayers])
        } else if (
          deployedPlayersList.length === 0 &&
          isFetchDeployedPlayerSuccess
        ) {
          console.log('111L--')
          // console.log('puree--', { playersListData, isFetchListPlayerSuccess })
          setIsDeadEnd(true)
        }
        // setAllPlayers([...allPlayers, ...deployedPlayersList])
      }
    } else {
      console.log('22bh---', appliedFilters)
      setAllPlayers(deployedPlayersList)
    }
    if (deployedPlayersList.length === 0 && searchedTerm) {
      // setNoDataFound(true)
      // toast.error('no data found')
    } else if (deployedPlayersList.length > 0 && searchedTerm) {
      setNoDataFound(false)
    }
    // setTimeout(() => {
    //   setLoadingPlayers(false)
    // }, 2000)
    console.log('boovo--', appliedFilters)
  }, [deployedPlayersList])

  useEffect(() => {
    console.log({
      player2contract,
      player2contractabi,
      isGetPlayer2ContractSuccess,
      isGetPlayer2ContractError,
    })
  }, [isGetPlayer2ContractSuccess])

  useEffect(() => {
    return () => {
      setTxnHash('')
    }
  }, [])

  const buyforExternalWallet = async () => {
    setLoading(true)
    const promise = callWeb3Method(
      // methodName: string,
      'createDraftRequest',
      player1contract, // "0xb6000dffb543008bd7f9cda2615608828f0bf904"
      player1contractabi, // abi of player 1 contract
      [player2contract], // 0xa1eff55865575ddf81c2c5727490524dd56da5e4
    )
    promise
      .then((txn: any) => {
        setTxnHash(txn.hash)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        if (err.message === '406') {
          // toast.error(t('this functionality unavailable for internal users'))
          setTxnError(t('this functionality unavailable for internal users'))
        } else {
          // toast.error(err.reason || err.message)
          setTxnError(err.reason || err.message)
        }
      })
  }

  const buyforInternalWallet = async () => {
    console.log('')
  }

  const handleSearch = (value: string | undefined) => {
    // setAllPlayers([])
    // setSearchFocussed(true)
    // let request = {}
    // setSearchedTerm(value)
    // if (value) {
    //   request = {
    //     q: value,
    //     status_id: 3,
    //   }
    // } else {
    //   setSearchFocussed(false)
    //   request = {
    //     status_id: 3,
    //   }
    // }

    // console.log({ request })
    // dispatch(fetchDraftNewPlayers(request))
    // // setAppliedFilters({ ...appliedFilters, ...request })
    // // setAppliedFilters(request)
    setAllPlayers([])
    setIsDeadEnd(false)
    let request: any = {}
    setSearchedTerm(value)
    // if (value) {
    //   request = {
    //     limit: '10',
    //     offset: '0',
    //     q: value,
    //   }
    // } else {
    //   request = {
    //     limit: '10',
    //     offset: '0',
    //   }
    // }
    request = {
      limit: '10',
      offset: '0',
      q: value || '',
    }
    if (!value) {
      delete request.q
    }
    // setAppliedFilters({ ...appliedFilters, ...request })
    setAppliedFilters({ ...appliedFilters, ...request })
  }

  const handleReset = () => {
    setAllPlayers([])
    // dispatch(fetchDraftNewPlayers({ status_id: 3 }))
    // setAllPlayers([...allPlayers, ...deployedPlayersList])
    // dispatch(fetchDraftNewPlayers({ status_id: 3 }))
    const request: any = {
      // limit: '10',
      // offset: '0',
      // status_id: [3, 4],
      sorted_by: 'market_cap',
      reverse: 'True',
      status_id: [3, 4],
      limit: 10,
      offset: 0,
    }
    // setAppliedFilters({ ...appliedFilters, ...request })
    setAppliedFilters(request)
    setIsDeadEnd(false)
  }

  // useEffect(() => {
  //   setAllPlayers(deployedPlayersList)
  // }, [deployedPlayersList])

  useEffect(() => {
    return () => {
      dispatch(resetDraftNewPlayers())
    }
  }, [])

  const getUrlParams = (url: string, param1: string, param2: string) => {
    const url_string = url
    console.log({ url_string })
    if (url_string) {
      const newUrl = new URL(url_string)
      const obj: any = new Object()
      obj[param1] = newUrl.searchParams.get(param1)
      obj[param2] = newUrl.searchParams.get(param2)
      console.log({ obj })
      return obj
    }
    return {}
  }

  const handleJumpToPage = (head: string) => {
    // event.preventDefault()
    if (head === 'back') {
      const paginationParams = getUrlParams(
        previousDeployedPlayerListUrl,
        'limit',
        'offset',
      )
      setAppliedFilters({ ...appliedFilters, ...paginationParams })
    } else {
      const paginationParams = getUrlParams(
        nextDeployedPlayerListUrl,
        'limit',
        'offset',
      )
      console.log('HJTP---', {
        appliedFilters,
        paginationParams,
        nextDeployedPlayerListUrl,
      })
      if (nextDeployedPlayerListUrl) {
        setAppliedFilters({ ...appliedFilters, ...paginationParams })
      }
    }
  }

  const handleClose = () => {
    setTxnHash('')
  }

  const handleCloseBottomPopup = () => {
    setLoading(false)
    setSearchFocussed(false)
    setTxnHash('')
    setTargetPlayer('')
    setTxnError('')
    // onClose()
  }

  return (
    <section
      className={classNames(
        'new-draft',
        isMobile() ? 'maxwidth player-dashboard-container mt-0' : '',
      )}
    >
      <BottomPopup
        mode="wallet drafting-players-loader"
        isOpen={isLoading || searchFocussed || txnHash || targetPlayer}
      >
        <section className="new-draft vertical-flex draftee-fly">
          {targetPlayer && !txnHash && !txnError && !isLoading ? (
            <div className="remove-adminplayer-prompt-wrapper">
              {walletType ? (
                <>
                  <div className="new-draft-title">
                    {t('are you sure you want to add this player as draftee')}
                  </div>
                  <div
                    className={classNames(
                      'new-draft-title eth-address',
                      'success',
                    )}
                  >
                    {targetPlayer}
                  </div>
                  <div className="mt-10">
                    <SubmitButton
                      title={t('yes')}
                      className="m-0auto mt-20"
                      onPress={() => buyforExternalWallet()}
                    />
                    <SubmitButton
                      title={t('no')}
                      className="m-0auto mt-20 btn-disabled"
                      onPress={() => setTargetPlayer('')}
                    />
                  </div>
                </>
              ) : (
                <div>
                  <div className="txn-err-wrapper wallet-err-wrapper">
                    <ResponseAlert status={'Error'} />
                    <div className="input-feedback">
                      {t('service unavailable for internal users')}
                    </div>
                  </div>
                  <div
                    className="bottom-close"
                    style={{ width: '90%' }}
                    onClick={() => handleCloseBottomPopup()}
                  >
                    <div className="close-button">{t('close')}</div>
                  </div>
                </div>
              )}
            </div>
          ) : (
            <>
              <img
                loading="lazy"
                src={walletType === 'Metamask' ? MetamaskIcon : CoinbaseIcon}
                className="draftee-metamaskicon"
                alt="metamask-icon"
              />
              <div className="input-label approve-blockchain">
                {t('please approve the blockchain transaction') +
                  ' ' +
                  walletType}
              </div>
              {isLoading && !txnHash && !txnError ? (
                <div className="checkout-loader-wrapper draftee-propmt mt-40">
                  {/* <div className="input-label approve-blockchain">
                    {t('please approve the blockchain transaction') +
                      ' ' +
                      walletType}
                  </div> */}
                  <div className="loading-spinner">
                    <div className="spinner__circle">
                      <div className="spinner__circle-gradient"></div>
                      <div className="spinner__circle-inner"></div>
                    </div>
                  </div>
                  <div className="bottom-close">
                    <div
                      className="close-button"
                      onClick={handleCloseBottomPopup}
                    >
                      {t('close')}
                    </div>
                  </div>
                </div>
              ) : (
                <>
                  {txnHash ? (
                    <div
                      style={{ height: '50px' }}
                      className={classNames(
                        'add-draftee-success',
                        'web3action-success',
                      )}
                    >
                      <CheckCircleOutlinedIcon className="response-icon success-icon web3-success-check" />
                      <span>{t('transaction sent')}</span>
                      {txnHash && (
                        <a
                          className="tx-link button-box"
                          href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                          target="_blank"
                        >
                          {t('show transaction')}
                        </a>
                      )}
                    </div>
                  ) : (
                    <div className="txn-err-wrapper">
                      <CancelOutlinedIcon className="response-icon error-icon" />
                      <span>{t('transaction failed')}</span>
                      <div className="input-feedback">{txnError}</div>
                    </div>
                  )}

                  <div className="bottom-close">
                    <div
                      className="close-button"
                      onClick={handleCloseBottomPopup}
                    >
                      {t('close')}
                    </div>
                  </div>
                </>
              )}
            </>
          )}
        </section>
      </BottomPopup>
      <div
        className={classNames('new-draft-title', isMobile() ? 'd-none' : '')}
      >
        {t('find players')}
      </div>
      <SearchBar
        containerClass="p-0 w-none"
        onEdit={handleSearch}
        onClose={() => handleReset()}
      />
      {allPlayers.length > 0 ? (
        <div className="list-header">
          <div>{t('name')}</div>
          {/* <div>{t('amount drafted')}</div> */}
        </div>
      ) : null}
      {allPlayers.length > 0 ? (
        <div>
          <InfiniteScroll
            className="player-list-pagination"
            dataLength={allPlayers.length}
            // next={fetchMoreData}
            next={() => handleJumpToPage('forth')}
            hasMore={hasMore}
            // loader={<h4>Loading...</h4>}
            height={isMobile() ? 740 : 600}
            // height={isMobile() ? 740 : 300}
            // loader={
            //   nextDeployedPlayerListUrl ? (
            //     <h4 className="text-center">{t('loading')}...</h4>
            //   ) : (
            //     <h4 className="text-center">. . .</h4>
            //   )
            // }
            loader={
              !isDeadEnd ? (
                <h4 className="text-center">{t('loading')}...</h4>
              ) : (
                <h4 className="text-center">. . .</h4>
              )
            }
            endMessage={
              <p style={{ textAlign: 'center' }}>
                <b>. . .</b>
              </p>
            }
            // endMessage={
            //   <p style={{ textAlign: 'center' }}>
            //     <b>Yay! You have seen it all</b>
            //   </p>
            // }
          >
            {/* {testItems.map((i: any, index: any) => (
                      <div style={style} key={index}>
                        div - #{index}
                      </div>
                    ))} */}
            {allPlayers.map((item: any, index: any) => (
              <div className="nft-item p-default" key={index}>
                <div className="nft-image-section">
                  <div className="image-border">
                    <img
                      loading="lazy"
                      src={item.playerpicture}
                      alt=""
                      className="nft-image"
                    />
                  </div>
                  <div className="nft-name">{item.name}</div>
                </div>
                <div className="price-button-section">
                  <div className="nft-price-section">
                    <div className="white-color">
                      {/* {NumberFormat(item.price)} */}
                    </div>
                    <div className="nft-price">
                      {/* ${NumberFormat(item.price)} */}
                    </div>
                  </div>
                  <AddIcon
                    className="green-color"
                    onClick={() => handleDraftPlayer(item)}
                  />
                </div>
              </div>
            ))}
          </InfiniteScroll>
        </div>
      ) : isDraftPlayersLoading ? (
        <div className="nft-item no-data">
          <div
            className={classNames(
              'all-players-loading',
              // isLoading ? '' : 'hidden',
            )}
          >
            <div className="spinner__circle">
              <div className="spinner__circle-gradient"></div>
              <div className="spinner__circle-inner"></div>
            </div>
          </div>
        </div>
      ) : (
        <div className="drafts-no-action-container no-new-draft">
          {isGetPlayerError ? (
            <div className="nft-price">{t('no data found')}</div>
          ) : null}
          <div>{t('no results found')}</div>
        </div>
      )}
    </section>
  )
}

export default NewDraft
