import React, { useEffect, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import { NumberFormat } from '@root/utils/NumberFormat'
import SubmitButton from '@components/SubmitButton'
import { useSelector } from 'react-redux'
import { RootState } from '@root/store/rootReducers'
import classnames from 'classnames'
import maticIcon from '@assets/images/matic-token-icon.png'
import TooltipLabel from '@components/TooltipLabel'
import {
  convertToFixed,
  getCountryName,
  getTimeFromDate,
  truncateDecimals,
} from '@utils/helpers'
import toast from 'react-hot-toast'
import HotToaster from '@components/HotToaster'
import PlayerImage from '@components/PlayerImage'
import 'flag-icons/css/flag-icons.min.css'
interface Props {
  card: any //CardProps
  disabledPlayer?: any
  onBuy: any
  onStake: any
  onSell: any
  onChartView: any
  isPlayerNotLaunched: boolean
  // statsData: any
  launchDate: any
  marketCap: any
}

const refObj = {
  '24h_change': '',
  coin_issued: '',
  holders: '',
  id: '',
  matic: '',
}

const PlayerInfo: React.FC<Props> = ({
  card,
  launchDate = 0,
  marketCap = 0,
  disabledPlayer,
  onBuy,
  onStake,
  onSell,
  onChartView,
  isPlayerNotLaunched,
}) => {
  // const { playercontractsubscriptionstart, market_cap } = statsData
  const { t } = useTranslation()
  const changeCountRef = useRef<any>(refObj)
  const playerStatsData = useSelector((state: RootState) => state.playerstats)
  const { fetchPlayerStatsRateData } = playerStatsData

  // const getUsdFromMatic = (maticValue: number) => {
  //   const currency = (maticValue * fetchPlayerStatsRateData.rate).toFixed(2)
  //   if (!isNaN(parseFloat(currency))) {
  //     return currency
  //   }
  //   return '00.00'
  // }
  function usePrevious(value: any) {
    const ref = useRef<any>(refObj)
    useEffect(() => {
      ref.current = value
    })
    return ref.current
  }

  const getUsdFromMatic = (maticValue: number) => {
    let currency = null
    // if (fetchPlayerStatsRateData) {
    //   currency = maticValue * card?.exchangeRateUSD?.rate
    // } else {
    //   currency = maticValue * card?.exchangeRateUSD?.rate
    // }
    let prevCurrency = prevAmount.matic * prevAmount.exchangeRateUSD?.rate
    prevCurrency = truncateDecimals(prevCurrency, 4)
    currency = maticValue * card?.exchangeRateUSD?.rate
    if (!isNaN(currency)) {
      // return truncateDecimals(currency, 4)
      const usdData = {
        prevCurrency,
        currentCurrency: truncateDecimals(currency, 4),
      }
      console.log({ usdData })
      return usdData
    }
    return {
      prevCurrency: '0.0000',
      currentCurrency: '0.0000',
    }
  }

  const prevAmount = usePrevious(card)
  // const prevUsd = usePrevious(getUsdFromMatic)

  useEffect(() => {
    changeCountRef.current = card
    console.log('JDF--', { card })
  }, [card])

  const getMarketValue = () => {
    let prev_currency = prevAmount?.matic * prevAmount?.exchangeRateUSD?.rate
    prev_currency = truncateDecimals(prevAmount.coin_issued * prev_currency, 4)
    const currency = card.matic * card?.exchangeRateUSD?.rate
    // const usd: any = getUsdFromMatic(card.matic)
    const marketData = {
      prev_currency,
      currency: truncateDecimals(card.coin_issued * currency, 4),
    }
    console.log({ marketData })
    return marketData
    // return truncateDecimals(totalCoins * currency, 4)
  }

  const showMessage = () => {
    toast.error(t('player coin is not launched'))
  }

  const handleTransfermarkt = () => {
    window.open(card?.transfermarkt_link, '_blank')
  }

  const getHumanDate = (date: any) => {
    const unixDate: any = new Date(date * 1000)
    let day = unixDate.getDate()
    let month = unixDate.getMonth() + 1
    const year = unixDate.getFullYear()
    if (day < 10) day = '0' + day
    if (month < 10) month = '0' + month

    return day + '.' + month + '.' + year
  }

  return (
    <>
      <HotToaster />
      <div className="player-card">
        <div className="fixed-content">
          <div className="img">
            <PlayerImage
              src={card.playerpicture || card.img}
              className="img-radius"
            />
            <div className="player-extend-buttons">
              <div
                className="chart-view-button"
                onClick={disabledPlayer ? showMessage : onChartView}
              ></div>
              {card?.transfermarkt_link && (
                <div
                  className="transfermarkt-button"
                  onClick={handleTransfermarkt}
                ></div>
              )}
            </div>
          </div>
          <div className="name">
            {card.name}
            <TooltipLabel title={getCountryName(card.country_code)}>
              <span className={`fi fi-${card.country_code}`}></span>
            </TooltipLabel>
          </div>
          {isPlayerNotLaunched ? (
            <div className="coming-soon h-3">{t('coming soon')}</div>
          ) : (
            <div className="player-cost">
              <div className="matic-wrapper details">
                <span
                  className={classnames(
                    'player-cost-stats',
                    // changeCountRef?.current['matic'] < card['matic']
                    //   ? 'profit'
                    //   : 'loss',
                    !prevAmount?.matic || prevAmount?.matic === card.matic
                      ? ''
                      : card.matic > prevAmount.matic
                      ? 'profit'
                      : 'loss',
                  )}
                >
                  {disabledPlayer ? '0.00' : truncateDecimals(card.matic, 5)}
                </span>
                <TooltipLabel title="MATIC">
                  <img src={maticIcon} alt="" />
                </TooltipLabel>
              </div>
              <span>&nbsp;</span>
              <span
                className={classnames(
                  'player-cost-stats',
                  !getUsdFromMatic(card.matic).currentCurrency ||
                    getUsdFromMatic(card.matic).currentCurrency ===
                      getUsdFromMatic(card.matic).prevCurrency
                    ? ''
                    : getUsdFromMatic(card.matic).currentCurrency >
                      getUsdFromMatic(card.matic).prevCurrency
                    ? 'profit'
                    : 'loss',
                )}
              >
                $
                {disabledPlayer
                  ? '0.00'
                  : getUsdFromMatic(card.matic).currentCurrency}
              </span>
            </div>
          )}
          <div className="player-stake">
            <span>{t('your stake')}: </span>
            <span className="green-color">0.00</span>
          </div>
          <div className="divide"></div>
          <div className="profile-info capitalize">
            <div>{t('coins in circulation')}:</div>
            <div
              // className="player-info-stats"
              className={classnames(
                'player-info-stats',
                // changeCountRef?.current['coin_issued'] < card['coin_issued']
                //   ? 'profit'
                //   : 'loss',
                !prevAmount?.coin_issued ||
                  prevAmount?.coin_issued === card.coin_issued
                  ? ''
                  : card.coin_issued > prevAmount.coin_issued
                  ? 'profit'
                  : 'loss',
              )}
            >
              {card.coin_issued && !isPlayerNotLaunched
                ? convertToFixed(card.coin_issued)
                : 0}
            </div>
          </div>
          <div className="profile-info">
            <div>{t('mecarreira market value')}:</div>
            <div
              className={classnames(
                'player-info-stats',
                !prevAmount?.coin_issued ||
                  getMarketValue().currency === getMarketValue().prev_currency
                  ? ''
                  : getMarketValue().currency > getMarketValue().prev_currency
                  ? 'profit'
                  : 'loss',
              )}
            >
              {card?.coin_issued && !isPlayerNotLaunched
                ? getMarketValue().currency
                : 0}
            </div>
            {/* market_cap coming 0 in ticker api */}
            {/* <div className={classnames('player-info-stats')}>
              {marketCap && !isPlayerNotLaunched ? marketCap.toFixed(3) : 0}
            </div> */}
          </div>
          <div className="changed-price">
            <div>{t('coin issue date')}:</div>
            <div className="player-info-stats date-value">
              {launchDate !== 0 && !isPlayerNotLaunched
                ? getHumanDate(launchDate)
                : t('not launched')}
            </div>
          </div>
          <div className="profile-info">
            <div>{t('nfts issued')}:</div>
            <div className="player-info-stats">
              {(card?.nftIssued && !isPlayerNotLaunched) || 0}
            </div>
          </div>
          <div className="button-line">
            <SubmitButton
              isDisabled={false}
              title={t('buy')}
              className={classnames(
                'button-box',
                disabledPlayer ? 'btn-disabled' : '',
              )}
              onPress={disabledPlayer ? showMessage : onBuy}
              // onPress={onBuy}
            />
            <SubmitButton
              isDisabled={false}
              title={t('stake coin')}
              className="button-box btn-disabled"
              onPress={
                disabledPlayer
                  ? showMessage
                  : () => {
                      console.log('')
                    }
              }
            />
            <SubmitButton
              isDisabled={false}
              title={t('sell')}
              className={classnames(
                'button-box',
                disabledPlayer ? 'btn-disabled' : '',
              )}
              onPress={disabledPlayer ? showMessage : onSell}
            />
          </div>
        </div>
      </div>
    </>
  )
}

export default PlayerInfo
