import FormInput from '@components/Form/FormInput'
import SubmitButton from '@components/SubmitButton'
import { useContext, useEffect, useState } from 'react'
import { monthSet } from '@root/constants'
import { useTranslation } from 'react-i18next'
import { FieldArray, Formik, ErrorMessage } from 'formik'
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined'
import * as Yup from 'yup'
import { callWeb3Method } from '@utils/helpers'
import { useSelector } from 'react-redux'
import { RootState } from '@root/store/rootReducers'
import { ConnectContext } from '@root/WalletConnectProvider'
import BottomPopup from '@components/BottomPopup'
import classNames from 'classnames'
import ResponseAlert from '@components/ResponseAlert'
import MetamaskIcon from '@assets/icons/icon/metamask.svg'
import CoinbaseIcon from '@assets/icons/icon/coinbase.svg'

const daysInMonth = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
const today = new Date()
const targetDate = new Date(
  today.getFullYear(),
  today.getMonth(),
  today.getDate(),
)
interface Years {
  id: number
  value: number
  title: number
}

const initialValues = {
  daySelected: '',
  monthSelected: '',
  yearSelected: '',
  question: '',
  answer: ['', ''],
}

const CreatePoll = () => {
  const { t } = useTranslation()
  const [daySelected, setDaySelected] = useState(targetDate.getDate())
  const [monthSelected, setMonthSelected] = useState(targetDate.getMonth())
  const [yearSelected, setYearSelected] = useState(targetDate.getFullYear())
  const [yearSet, setYearSet] = useState<Years[]>([])
  const [daysSet, setDaysSet] = useState<Years[]>([])
  const [isCreatePollPopup, setCreatePollPopup] = useState<boolean>(false)
  const [txnHash, setTxnHash] = useState<string>('')
  const [txnErr, setTxnErr] = useState('')
  const { getBlockNumber } = useContext(ConnectContext)

  const playerCoinData = useSelector((state: RootState) => state.playercoins)

  const { player1contract, player1contractabi } = playerCoinData

  useEffect(() => {
    window.scrollTo(0, 0)
    setDate(targetDate)
    setYears(20)
  }, [])

  function setDate(date: any) {
    setDays(date.getMonth())
    setDaySelected(date.getDate())
    setMonthSelected(date.getMonth())
    setYearSelected(date.getFullYear())
  }

  function setDays(monthIndex: number) {
    const optionCount = 0
    const daysCount = daysInMonth[monthIndex]
    const daysArr = []
    if (optionCount < daysCount) {
      for (let i = optionCount; i < daysCount; i++) {
        daysArr.push({
          id: i + 1,
          value: i + 1,
          title: i + 1,
        })
      }
      setDaysSet(daysArr)
    } else {
      for (let i = daysCount; i < optionCount; i++) {
        const optionItem = '#select-day option[value=' + (i + 1) + ']'
      }
    }
  }

  function setYears(val: number) {
    const year = targetDate.getFullYear()
    const yearArr = []
    for (let i = 0; i < val; i++) {
      yearArr.push({
        id: i + 1,
        value: year + i,
        title: year + i,
      })
    }
    setYearSet(yearArr)
  }

  const handleDayChange = (evt: any) => {
    setDaySelected(evt.target.value)
  }

  const handleMonthChange = (evt: any) => {
    setMonthSelected(evt.target.value)
    const monthIndex = monthSelected
    setDays(evt.target.value)
  }

  const handleYearChange = (evt: any) => {
    setYearSelected(evt.target.value)
  }

  const validate = (values: any) => {
    const errors: any = {}
    if (
      new Date(
        values.yearSelected +
          '-' +
          values.monthSelected +
          '-' +
          values.daySelected,
      ).getTime() < new Date().getTime()
    ) {
      errors.yearSelected = t('the closing voting date')
    }
    return errors
  }

  const handleClose = () => {
    setCreatePollPopup(false)
    setTxnErr('')
    setTxnHash('')
  }

  const handleCreatePoll = (values: any) => {
    if (!player1contract) {
      return
    }

    const seconds =
      (new Date(
        values.yearSelected +
          '-' +
          values.monthSelected +
          '-' +
          values.daySelected,
      ).getTime() -
        new Date().getTime()) /
      1000
    getBlockNumber().then(function (result: any) {
      const blockNumber = result + Math.floor(seconds / 2)
      console.log({ blockNumber })
      setCreatePollPopup(true)
      const promise = callWeb3Method(
        'createPoll',
        player1contract,
        player1contractabi,
        [values.question, values.answer, blockNumber],
      )
      promise
        .then(async (txn: any) => {
          setTxnHash(txn.hash)
        })
        .catch((err: any) => {
          if (err.message === '406') {
            // toast.error(t('this functionality unavailable for internal users'))
            setTxnErr(t('this functionality unavailable for internal users'))
          } else {
            // toast.error(err.reason || err.message)
            setTxnErr(err.reason || err.message)
          }
          // toast.error(err.reason || err.message)
        })
    })
  }

  return (
    <>
      {isCreatePollPopup && (
        <BottomPopup
          mode="wallet drafting-players-loader vertical-flex"
          isOpen={isCreatePollPopup}
        >
          <img
            loading="lazy"
            src={
              localStorage.getItem('wallet') === 'Metamask'
                ? MetamaskIcon
                : CoinbaseIcon
            }
            alt="metamask-icon"
          />
          {!txnHash && !txnErr ? (
            <div className="checkout-loader-wrapper">
              <div className="input-label approve-blockchain">
                {t('please approve the blockchain transaction') +
                  ' ' +
                  localStorage.getItem('wallet')}
              </div>
              <div className="loading-spinner">
                <div className="spinner__circle">
                  <div className="spinner__circle-gradient"></div>
                  <div className="spinner__circle-inner"></div>
                </div>
              </div>
              <div className="bottom-close w3actionprompt-close">
                <div className="close-button" onClick={handleClose}>
                  {t('close')}
                </div>
              </div>
            </div>
          ) : (
            <>
              {txnHash ? (
                <div
                  className={classNames(
                    'purchase-submit-wrapper',
                    'btn-purchase',
                    'web3action-success',
                  )}
                >
                  <ResponseAlert status={'Success'} />
                  {txnHash && (
                    <a
                      className="tx-link"
                      href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                      target="_blank"
                    >
                      {t('show transaction')}
                    </a>
                  )}
                </div>
              ) : (
                <div className="txn-err-wrapper">
                  <ResponseAlert status={'Error'} />
                  <div className="input-feedback">{txnErr}</div>
                </div>
              )}

              <div className="bottom-close">
                <div className="close-button" onClick={handleClose}>
                  {t('close')}
                </div>
              </div>
            </>
          )}
        </BottomPopup>
      )}
      <div className="createpoll-input-container">
        <div className="nft-tab-title">{t('create a new voting poll')}</div>
        <Formik
          initialValues={initialValues}
          onSubmit={handleCreatePoll}
          validate={validate}
          validationSchema={Yup.object().shape({
            daySelected: Yup.string().required(t('date is required')),
            monthSelected: Yup.string().required(t('month is required')),
            yearSelected: Yup.string().required(t('year is required')),
            question: Yup.string().required(t('question is required')),
            answer: Yup.array().of(
              Yup.string().required(t('answer is required')),
            ),
          })}
        >
          {props => {
            const {
              values,
              touched,
              errors,
              isValid,
              dirty,
              handleChange,
              handleBlur,
              handleSubmit,
              setFieldValue,
            } = props
            return (
              <form autoComplete="off" onSubmit={handleSubmit}>
                <div className="createpoll-input-item">
                  <div className="input-label">{t('vote closes on')}</div>
                  <div className="birthday" style={{ marginTop: '6px' }}>
                    <select
                      id="select-day"
                      className="dob-select"
                      value={values.daySelected}
                      name="daySelected"
                      // onChange={handleChange}
                      onChange={(event: any) => {
                        handleChange(event)
                        // calculateTotal(event)
                        handleDayChange(event)
                      }}
                      onBlur={handleBlur}
                    >
                      <option>DD</option>
                      {daysSet.map(({ value, title }, index) => (
                        <option key={index} value={value}>
                          {title}
                        </option>
                      ))}
                    </select>
                    <select
                      id="select-month"
                      className="dob-select"
                      value={values.monthSelected}
                      name="monthSelected"
                      // onChange={handleChange}
                      onChange={(event: any) => {
                        handleChange(event)
                        // calculateTotal(event)
                        handleMonthChange(event)
                      }}
                      onBlur={handleBlur}
                    >
                      <option>MM</option>
                      {monthSet.map(({ value, title }, index) => (
                        <option key={index} value={value}>
                          {title}
                        </option>
                      ))}
                    </select>
                    <select
                      id="select-year"
                      className="dob-select"
                      value={values.yearSelected}
                      name="yearSelected"
                      // onChange={handleChange}
                      onChange={(event: any) => {
                        handleChange(event)
                        // calculateTotal(event)
                        handleYearChange(event)
                      }}
                      onBlur={handleBlur}
                    >
                      <option>YYYY</option>
                      {yearSet.map(({ value, title }, index) => (
                        <option key={index} value={value}>
                          {title}
                        </option>
                      ))}
                    </select>
                  </div>
                  {errors.daySelected && touched.daySelected && (
                    <div className="input-feedback">{errors.daySelected}</div>
                  )}
                  {errors.yearSelected && touched.yearSelected && (
                    <div className="input-feedback">{errors.monthSelected}</div>
                  )}
                  {errors.yearSelected && touched.yearSelected && (
                    <div className="input-feedback">{errors.yearSelected}</div>
                  )}
                </div>
                <div className="createpoll-input-item">
                  <div className="input-label">{t('voting question')}</div>
                  <textarea
                    className="createpoll-textarea"
                    placeholder={t('enter voting question')}
                    name="question"
                    value={values.question}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  ></textarea>
                </div>
                {errors.question && touched.question && (
                  <div className="input-feedback">{errors.question}</div>
                )}
                <FieldArray
                  name="answer"
                  render={arrayHelpers => (
                    <div>
                      {values.answer.map((item, index) => (
                        <>
                          <div className="createpoll-input-item" key={index}>
                            <div className="input-label">
                              {t('answer')}
                              {index + 1}
                            </div>
                            <FormInput
                              type="text"
                              placeholder={t('enter answer')}
                              name={`answer.${index}`}
                              maxLength={25}
                              value={values.answer[index]}
                              handleChange={handleChange}
                              onBlur={handleBlur}
                            />
                            {index > 0 && (
                              <DeleteForeverOutlinedIcon
                                onClick={() => arrayHelpers.remove(index)}
                              />
                            )}
                          </div>
                          <ErrorMessage
                            name={`answer.${index}`}
                            component="div"
                            className="input-feedback"
                          />
                          {index === values.answer.length - 1 && (
                            <div
                              className="createpoll-answer-addbtn"
                              onClick={() => arrayHelpers.insert(index + 1, '')}
                            >
                              {t('add more answers')}
                            </div>
                          )}
                        </>
                      ))}
                    </div>
                  )}
                />
                <SubmitButton
                  isDisabled={false}
                  title={t('create voting poll')}
                  className="createpoll-createbtn"
                  onPress={handleSubmit}
                />
              </form>
            )
          }}
        </Formik>
      </div>
    </>
  )
}

export default CreatePoll
