import React, { useState, useEffect } from 'react'
import classnames from 'classnames'
import Web3 from 'web3'
import { ethers } from 'ethers'
import { Buffer } from 'buffer/'
import { useWeb3React } from '@web3-react/core'
import { signSmartContractData } from '@wert-io/widget-sc-signer'
import { useTranslation } from 'react-i18next'
import { v4 as uuidv4 } from 'uuid'
import ChevronRightIcon from '@mui/icons-material/ChevronRight'
import SearchIcon from '@mui/icons-material/Search'
import { Formik } from 'formik'
import * as Yup from 'yup'
import {
  API_CONSTANTS,
  IsDevelopment,
  PlayerCardData,
  POLYGON_NETWORK_PARAMS,
} from '@root/constants'
import { Input } from '@components/Form'
import SearchInput from '@components/Form/SearchInput'
import {
  callWeb3Method,
  isMobile,
  sleep,
  truncateDecimals,
} from '@utils/helpers'
import Spinner from '@components/Spinner'
import ResponseAlert from '@components/ResponseAlert'
import NftSummary from '../components/NftSummary'
import PurchaseButton from '../components/PurchaseButton'
import { useDispatch, useSelector } from 'react-redux'
import {
  changeNftValue,
  fetchPurchaseDetails,
  getWalletAddress,
  getWalletDetails,
  setActiveTab,
  showSignupForm,
  showWalletForm,
} from '@root/apis/onboarding/authenticationSlice'
import { RootState } from '@root/store/rootReducers'
import '@assets/css/pages/PurchaseNft.css'
import classNames from 'classnames'
import {
  fetchPlayersStats,
  fetchPlayersStatsError,
  fetchSinglePlayerStats,
  resetStatsError,
} from '@root/apis/playerStats/playerStatsSlice'
import CreditCardButton from '../components/CreditCardButton'
import WertWidget from '@wert-io/widget-initializer'
import TooltipLabel from '@components/TooltipLabel'
import maticIcon from '@assets/images/matic-token-icon.png'
import visaIcon from '@assets/images/visa-white.png'
import masterCardIcon from '@assets/images/mastercard-large.png'
import BottomPopup from '@components/BottomPopup'
import PaymentMethodCard from '../components/PaymentMethodCard'
import { useNavigate } from 'react-router'
import toast from 'react-hot-toast'
import HotToaster from '@components/HotToaster'
import {
  getPlayer1Contract,
  getPlayer2Contract,
  initPlayer1ContractStatus,
} from '@root/apis/playerCoins/playerCoinsSlice'
import MetamaskIcon from '@assets/icons/icon/metamask.svg'
import CoinbaseIcon from '@assets/icons/icon/coinbase.svg'
import CheckCircleOutlinedIcon from '@mui/icons-material/CheckCircleOutlined'
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined'

interface Props {
  playerData?: any
  onClosePopup: any
  onSelectCreditCard?: any
}

interface signinData {
  address: string
  commodity: string
  commodity_amount: any
  pk_id: string
  sc_address: string
  sc_id: string
  sc_input_data: string
}
interface WertOptions {
  partner_id: string
  container_id: string
  commodities?: any
  commodity: string
  click_id: any
  origin?: string
  extra?: any
  listeners?: any
  color_background?: string
  color_buttons?: string
  color_buttons_text?: string
  color_secondary_buttons?: string
  color_secondary_buttons_text?: string
  color_main_text?: string
  color_secondary_text?: string
  color_icons?: string
  color_links?: string
  color_success?: string
  color_warning?: string
  color_error?: string
}

const jsonInterface: any = [
  {
    inputs: [
      { internalType: 'address', name: '_playerContract', type: 'address' },
      { internalType: 'address', name: '_buyOnBehalf', type: 'address' },
    ],
    name: 'callMeCarreiraContract',
    outputs: [],
    stateMutability: 'payable',
    type: 'function',
  },
]

const buyNftInterval: any = null
const BuyNftForm: React.FC<Props> = ({
  playerData = null,
  onClosePopup,
  onSelectCreditCard = null,
}) => {
  // @ts-ignore
  window.Buffer = Buffer
  const navigate = useNavigate()
  const loginInfo = localStorage.getItem('loginInfo')
  const loginId = localStorage.getItem('loginId')
  const walletType = localStorage.getItem('wallet')
  const dispatch = useDispatch()
  const { t } = useTranslation()
  const [isSearchEnabled, setSearchEnabled] = useState(false)
  const [isLoading, setLoading] = useState(false)
  const [txnHash, setTxnHash] = useState('')
  const [txnError, setTxnError] = useState('')
  const [buttonTitle, setButtonTitle] = useState('buy')
  const [response, setResponse] = useState('')
  const [stopCalculation, setStopCalculation] = useState(false)
  const [fixedPrice, setFixedPrice] = useState('0.5')
  const [totalSum, setTotalSum] = useState('0')
  const [inProgress, setInProgress] = useState(false)
  const [isCalculating, setIsCalculating] = useState(false)
  const [inputAmount, setInputAmount] = useState('0')
  const [amountLoading, setAmountLoading] = useState(false)
  const [loadingCheckout, setLoadingCheckout] = useState(false)
  const [widgetInitiated, setWidgetInitiated] = useState(false)
  const [toBuy, setToBuy] = useState(false)
  const [usdTotal, setUsdTotal] = useState('')
  const [isLoadingMatic, setIsLoadingMatic] = useState(false)
  const [isDisabledMethod, setIsDisabledMethod] = useState(false)
  const [txHash, setTxHash] = useState('')
  const [isPurchaseLoading, setPurchaseLoading] = useState(false)
  const [count, setCount] = useState(0)
  const [callbackValue, setCallbackValue] = useState('')
  const [delayedLaunching, setDelayedLaunching] = useState(true)
  const userWalletAddress = localStorage.getItem('userWalletAddress')
  const authenticationData = useSelector(
    (state: RootState) => state.authentication,
  )
  const playerStatsData = useSelector((state: RootState) => state.playerstats)
  const {
    fetchSinglePlayerStatsData,
    fetchSinglePlayerStatsRateData,
    fetchSinglePlayerStatsError,
    isProgress,
  } = playerStatsData
  const purchaseData = useSelector((state: RootState) => state.purchases)
  const {
    player1contract,
    player1contractabi,
    isGetPlayer1ContractSuccess,
    isGetPlayer1ContractError,
    player2contract,
  } = useSelector((state: RootState) => state.playercoins)

  const {
    loader,
    userWalletData: { USDBalance, address, balance, exchangeRateData },
  } = authenticationData

  const handleSearch = () => {
    setSearchEnabled(true)
  }
  const handleClose = () => {
    setSearchEnabled(false)
  }

  const context = useWeb3React()
  useEffect(() => {
    dispatch(getWalletAddress(loginInfo))
    dispatch(getPlayer2Contract(playerData?.detailpageurl))
    setTimeout(() => {
      setDelayedLaunching(false)
    }, 4000)
    return () => {
      clearInterval(buyNftInterval)
      dispatch(resetStatsError())
    }
  }, [])

  const handleBuy = async () => {
    if (loginId || loginInfo) {
      if (address || loginInfo || userWalletAddress) {
        console.log({ address, loginInfo, userWalletAddress })
        setStopCalculation(true)
        await clearInterval(buyNftInterval)
        setToBuy(true)
      } else {
        // return
        toast.error(t('please create a wallet first'))
        if (isMobile()) {
          navigate('/wallet')
        } else {
          await handleClosePopup()
          dispatch(showWalletForm({ isMandatory: true }))
        }
      }
    } else {
      toast.error(t('please login to continue'))
      if (!isMobile()) {
        await handleClosePopup()
      }
      dispatch(setActiveTab('login'))
      if (isMobile()) {
        navigate('/signup')
      } else {
        dispatch(showSignupForm())
      }
    }
  }

  const handleClosePopup = () => {
    onClosePopup()
  }

  const handleGetPriceStats = (playersData: any) => {
    dispatch(
      fetchSinglePlayerStats({
        contracts: playersData,
        query: 'RPC',
        formType: 'BUY',
        amount: parseInt(inputAmount) > 0 ? inputAmount : 1,
      }),
    )
  }

  useEffect(() => {
    if (playerData.playercontract) {
      handleGetPriceStats([playerData.playercontract])
      clearInterval(buyNftInterval)
    }
  }, [playerData])

  const calculateTotal = async () => {
    setCount(count + 1)
    let total: any = null
    if (inputAmount !== '0' && inputAmount !== '') {
      total =
        parseFloat(fetchSinglePlayerStatsData[0]?.matic) *
        parseFloat(inputAmount)
    } else {
      total = parseFloat(fetchSinglePlayerStatsData[0]?.matic) * 1
    }

    const absTotal = parseFloat(total).toFixed(5) //truncateDecimals(total, 4)
    if (!delayedLaunching) {
      setIsCalculating(true)
    }
    for (let i = 0; i < 2; i++) {
      await sleep(i * 1000)
    }
    // setInputAmount(event.target.value)
    if (!delayedLaunching) {
      setIsCalculating(false)
    }
    console.log({ absTotal })
    setTotalSum(absTotal.toString())
  }

  useEffect(() => {
    getBuyDetails()
    dispatch(getWalletDetails()) //COMMENTED FOR PROD
  }, [])

  useEffect(() => {
    calculateTotal()
  }, [fetchSinglePlayerStatsData[0]])

  useEffect(() => {
    console.log({ fetchSinglePlayerStatsData })
  }, [fetchSinglePlayerStatsData])

  useEffect(() => {
    // const delayDebounceFn = setTimeout(() => {
    //   calculateTotal()
    //   // Send Axios request here
    // }, 2000)
    dispatch(
      fetchSinglePlayerStats({
        contracts: [playerData.playercontract],
        query: 'RPC',
        formType: 'BUY',
        amount: parseInt(inputAmount) > 0 ? inputAmount : 1,
      }),
    )
    // return () => clearTimeout(delayDebounceFn)
  }, [inputAmount])

  const getBuyDetails = async () => {
    setInProgress(true)
    for (let i = 0; i < 2; i++) {
      await sleep(i * 1000)
    }
    setInProgress(false)
  }

  const handleAsync = async (event: any) => {
    setAmountLoading(true)
    for (let i = 0; i < 2; i++) {
      await sleep(i * 1000)
    }
    setInputAmount(event.target.value)
    setAmountLoading(false)
  }

  const getCallback = (value: string) => {
    setCallbackValue(value)
    console.log('GBC--', value, playerData)
    handleGetPriceStats([playerData.playercontract])
  }

  const handleWertInput = () => {
    if (IsDevelopment) {
      let iframeElement: any = null
      onSelectCreditCard('hide')
      setInProgress(true)
      setWidgetInitiated(true)
      setToBuy(false)

      const web3 = new Web3(Web3.givenProvider)

      const meCarreiraContract = new web3.eth.Contract(
        jsonInterface,
        '0xcC25b6eDea2584AB862B05c6dD671C2f39027a41', // proxy address given by alex in chat
      )
      console.log({ meCarreiraContract })
      console.log('totalSum---', truncateDecimals(parseFloat(totalSum), 2))

      const web3jsSc = meCarreiraContract.methods
        .callMeCarreiraContract(
          player2contract, //'0x6d963d5b7a4949b90a7fd970edfff14609fddeb3', // player coin address
          address, // my wallet address
        )
        .encodeABI()
      console.log('sc_input---', web3jsSc)

      const privateKey =
        '0x57466afb5491ee372b3b30d82ef7e7a0583c9e36aef0f02435bd164fe172b1d3' // sandbox key given by alex

      const signOptions: signinData = {
        address: address, //'0x444546De6cD89215b74F386D47DB04311b3AF839',
        commodity: 'MATIC',
        commodity_amount: truncateDecimals(parseFloat(totalSum), 2), //totalSum, //'2.05',
        pk_id: 'key1',
        sc_address: '0xcC25b6eDea2584AB862B05c6dD671C2f39027a41', // proxy address given by alex in chat
        sc_id: uuidv4(),
        sc_input_data: web3jsSc,
      }

      const signedData = signSmartContractData(signOptions, privateKey)

      const widgetOptions: WertOptions = {
        partner_id: '01G2A9N1TZ18NWM0EGCYFX9E33', //partner id given by alex over email
        container_id: 'purchase-box',
        click_id: uuidv4(),
        origin: 'https://sandbox.wert.io',
        commodity: 'MATIC:Polygonmumbai',
        color_background: '#212435',
        color_buttons: '#81df0d',
        color_buttons_text: '#212435',
        color_main_text: '#abacb5',
        listeners: {
          position: (data: any) => console.log('step:', data.step),
          close: (evt: any) => console.log('CLOSE_WERT:', evt),
          error: (error: any) => console.log('ERROR_WERT:', error),
          loaded: (loaded: any) => {
            console.log('LOADED_WERT:', loaded)
            iframeElement[0].style.zIndex = 'unset'
          },
          position: (posn: any) => console.log('POSITION_WERT:', posn),
        },
      }

      const wertWidget = new WertWidget({
        ...signedData,
        ...widgetOptions,
      })
      let mountTime: any = null
      clearTimeout(mountTime)
      wertWidget.mount()
      iframeElement = document.getElementsByTagName('IFRAME')
      mountTime = setTimeout(() => {
        // setToBuy(false)
        // setLoadingCheckout(false)
        setInProgress(false)
      }, 5000)
    } else {
      toast.error(t('this feature is not yet enabled'))
    }
  }

  const handleMaticSend = () => {
    if (IsDevelopment) {
      setTxHash('')
      setTxnError('')
      setTxnHash('')
      setPurchaseLoading(true)
      if (walletType) {
        setIsLoadingMatic(true)
      }
      // setIsLoadingMatic(true)
      setIsDisabledMethod(true)
      setTxHash('')
      dispatch(getPlayer1Contract(playerData?.detailpageurl))
    } else {
      toast.error(t('this feature is not yet enabled'))
    }
  }

  const buyforExternalWallet = async () => {
    let walletProvider = window.ethereum?.providers?.find((provider: any) =>
      localStorage.getItem('wallet') === 'Metamask'
        ? provider.isMetaMask
        : localStorage.getItem('wallet') === 'Coinbase'
        ? provider.isCoinbaseWallet
        : provider.isTrustWallet,
    )
    if (!walletProvider) {
      if (
        (window.ethereum?.isMetaMask &&
          localStorage.getItem('wallet') === 'Metamask') ||
        (window.ethereum?.isCoinbaseWallet &&
          localStorage.getItem('wallet') === 'Coinbase')
      ) {
        walletProvider = window.ethereum
      }
    }
    // const provider = new ethers.providers.Web3Provider(window.ethereum)
    const provider = new ethers.providers.Web3Provider(walletProvider)
    console.log('signer', provider.getSigner(loginInfo!))
    const playerContract = new ethers.Contract(
      player1contract, // player contract address of the player coin
      player1contractabi, // player contract abi of the player coin
      provider.getSigner(loginInfo!), // signer of the currently logged in user wallet address
    )
    console.log('plcCont---', {
      playerContract,
      player1contract,
      player1contractabi,
    })
    try {
      const options = {
        value: ethers.utils.parseEther(totalSum),
      }
      console.log('buyOption---', totalSum)
      const tx = await playerContract.buyToken(options)
      console.log('Transaction hash is ', tx.hash)
      // setToBuy(false)
      setIsLoadingMatic(false)
      setIsDisabledMethod(false)
      setResponse('Success')
      setTxnHash(tx.hash)
      setButtonTitle('Close')
    } catch (err: any) {
      console.log('buy_token_exception', err)
      setTxnError(err.reason || err.message)
    }

    // const promise = callWeb3Method(
    //   'buyToken',
    //   player1contract,
    //   player1contractabi, // abi of player 1 contract
    //   [],
    // )
    // promise
    //   .then((txn: any) => {
    //     setTxnHash(txn.hash)
    //     setLoading(false)
    //   })
    //   .catch((err: any) => {
    //     setLoading(false)
    //     if (err.message === '406') {
    //       setTxnError(t('this functionality unavailable for internal users'))
    //     } else {
    //       setTxnError(err.reason || err.message)
    //     }
    //   })
  }

  const buyforInternalWallet = async () => {
    setTxnError(t('this functionality unavailable for internal users'))
  }

  const handleCloseBottomPopup = () => {
    setPurchaseLoading(false)
    // setInputAmount('0')
    setTxnHash('')
    setTxnError('')
    handleCloseToast()
    // onClose()
  }

  useEffect(() => {
    if (isGetPlayer1ContractSuccess) {
      // setIsLoadingMatic(false)
      // setIsDisabledMethod(false)
      if (loginInfo) {
        buyforExternalWallet()
      } else if (loginId) {
        buyforInternalWallet()
      }
      dispatch(initPlayer1ContractStatus())
    }
    // if (isGetPlayer1ContractSuccess) {
    //   toast.error(t('Loading player contract is failed.'))
    //   setIsLoadingMatic(false)
    //   setIsDisabledMethod(false)
    // }
  }, [isGetPlayer1ContractSuccess, isGetPlayer1ContractError])

  const fetchLiveTotalUsd = (value: string) => {
    setUsdTotal(value)
  }

  const handleCloseToast = () => {
    setToBuy(false)
    setStopCalculation(false)
    setIsLoadingMatic(false)
    setIsDisabledMethod(false)
  }

  const isFundsInsufficient = parseFloat(balance || '0') < parseFloat(totalSum)

  const handleAbortPurchase = () => {
    setPurchaseLoading(false)
    setIsLoadingMatic(false)
    handleCloseToast()
  }

  return (
    <div
      className={classNames(
        'purchase-container',
        widgetInitiated ? 'rounded-corners' : '',
        // loadingCheckout ? 'widget-wrapper' : '',
      )}
    >
      <HotToaster />
      <div
        id="purchase-box"
        className={classnames(widgetInitiated ? 'wert-widget-wrapper' : '')}
      ></div>
      {!fetchSinglePlayerStatsError ? (
        <div
          className={classnames(
            'balance-progress',
            loader ||
              inProgress ||
              fetchSinglePlayerStatsData[0]?.matic === undefined
              ? ''
              : 'hidden',
          )}
        >
          <div
            className={classnames(
              'loading-spinner-container mb-40 mt-40',
              'show',
            )}
          >
            <div className="loading-spinner">
              <div className="spinner__circle">
                <div className="spinner__circle-gradient"></div>
                <div className="spinner__circle-inner"></div>
              </div>
            </div>
          </div>
        </div>
      ) : null}
      <BottomPopup mode="wallet" isOpen={toBuy}>
        {isPurchaseLoading ? (
          <section className="new-draft vertical-flex buy-fly">
            {/* <img
              loading="lazy"
              src={walletType === 'Metamask' ? MetamaskIcon : CoinbaseIcon}
              className="draftee-metamaskicon"
              alt="metamask-icon"
            /> */}
            {walletType ? (
              <>
                <img
                  loading="lazy"
                  src={walletType === 'Metamask' ? MetamaskIcon : CoinbaseIcon}
                  className="draftee-metamaskicon"
                  alt="metamask-icon"
                />
                <div className="input-label approve-blockchain">
                  {t('please approve the blockchain transaction') +
                    ' ' +
                    walletType}
                </div>
              </>
            ) : (
              <div className="draftee-metamaskicon"></div>
            )}
            {isLoadingMatic && !txnHash && !txnError ? (
              <div className="checkout-loader-wrapper draftee-propmt mt-40">
                {/* <div className="input-label approve-blockchain">
                  {t('please approve the blockchain transaction') +
                    ' ' +
                    walletType}
                </div> */}
                <div className="loading-spinner">
                  <div className="spinner__circle">
                    <div className="spinner__circle-gradient"></div>
                    <div className="spinner__circle-inner"></div>
                  </div>
                </div>
                <div className="bottom-close">
                  <div className="close-button" onClick={handleAbortPurchase}>
                    {t('close')}
                  </div>
                </div>
              </div>
            ) : (
              <>
                {txnHash ? (
                  <div
                    style={{ height: '50px' }}
                    className={classNames(
                      'add-draftee-success',
                      'web3action-success',
                    )}
                  >
                    {/* <ResponseAlert status={'Success'} />
                    {txnHash && (
                      <a
                        className="tx-link"
                        href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                        target="_blank"
                      >
                        {t('show transaction')}
                      </a>
                    )} */}
                    <CheckCircleOutlinedIcon className="response-icon success-icon web3-success-check" />
                    <span>{t('transaction sent')}</span>
                    {txnHash && (
                      <a
                        className="tx-link button-box"
                        href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                        target="_blank"
                      >
                        {t('show transaction')}
                      </a>
                    )}
                  </div>
                ) : (
                  <div className="txn-err-wrapper">
                    {/* <ResponseAlert status={'Error'} />
                    <div className="input-feedback">{txnError}</div> */}
                    <CancelOutlinedIcon className="response-icon error-icon" />
                    <span>{t('transaction failed')}</span>
                    <div className="input-feedback">{txnError}</div>
                  </div>
                )}

                <div className="bottom-close">
                  <div
                    className="close-button"
                    onClick={handleCloseBottomPopup}
                  >
                    {t('close')}
                  </div>
                </div>
              </>
            )}
          </section>
        ) : (
          <div className="available-methods-container">
            <div className="terms-subtitle ct-h4 select-pay-title">
              {t('select payment method')}
            </div>
            <div className="methods-box">
              <PaymentMethodCard
                title={t('credit card')}
                logoSet={[
                  { id: 1, img: visaIcon, class: 'visa-method-logo' },
                  { id: 2, img: masterCardIcon, class: 'mc-method-logo' },
                ]}
                labelBottom={t('total estimated in USD')}
                valueBottom={usdTotal}
                unit="dollar"
                onSelect={handleWertInput}
                isDisabled={isDisabledMethod}
                // isDisabled={true}
              />
              <PaymentMethodCard
                title={t('cryptocurrency (matic)')}
                isCryptoLoading={isLoadingMatic}
                logoSet={[
                  { id: 3, img: maticIcon, class: 'matic-method-logo' },
                ]}
                labelBottom={t('total estimated')}
                valueBottom={totalSum}
                unit="matic"
                // isDisabled={true}
                isInsufficientMatics={isFundsInsufficient}
                onSelect={handleMaticSend}
              />
            </div>
            <div className="deposit-cancel" onClick={handleCloseToast}>
              {t('close')}
            </div>
          </div>
        )}
      </BottomPopup>
      <div
        className={classnames(
          'purchase-wrappper',
          (loader ||
            inProgress ||
            fetchSinglePlayerStatsData[0]?.matic === undefined) &&
            !fetchSinglePlayerStatsError
            ? 'hidden'
            : '',
        )}
      >
        <div className="player-title-bar">
          {isSearchEnabled ? (
            <SearchInput
              type="text"
              placeholder={`${t('please enter the search words')}.`}
              className="in-menu-search purchase-search"
              onChange={() => {
                return
              }}
              onClose={handleClose}
            />
          ) : (
            <>
              <div className="player-title-wrapper">
                <ChevronRightIcon className="icon-color" />
                <div>
                  <img
                    src={
                      playerData?.playerpicture ||
                      PlayerCardData[purchaseData?.purchasePlayerId]?.img
                    }
                  />
                </div>
                <div className="player-text-container">
                  <h6>
                    {playerData?.name ||
                      PlayerCardData[purchaseData?.purchasePlayerId]?.name}
                  </h6>
                  <h6>{playerData?.ticker}</h6>
                </div>
              </div>
              <SearchIcon className="icon-color" onClick={handleSearch} />
            </>
          )}
        </div>
        <Formik
          enableReinitialize={true}
          initialValues={{ price: '1' }}
          onSubmit={async values => {
            if (response === 'Success' || response === 'Error') {
              handleClosePopup()
            } else {
              handleBuy()
            }
          }}
          validationSchema={Yup.object().shape({
            price: Yup.string().required(t('required')),
          })}
        >
          {props => {
            const {
              values,
              touched,
              errors,
              handleChange,
              handleBlur,
              handleSubmit,
              setFieldValue,
            } = props
            return (
              <form onSubmit={handleSubmit} autoComplete={'off'}>
                <div className="purchase-form buy-form">
                  <div className="form-label-wrapper">
                    <label htmlFor="playerPrice" className="capitalize">
                      {t('buy')}
                    </label>
                    {errors.price && touched.price && (
                      <div className="input-feedback purchase-error">
                        {errors.price}
                      </div>
                    )}
                  </div>
                  <Input
                    id="buy_price"
                    name="price"
                    type={isMobile() ? 'number' : 'text'}
                    // disabled={isCalculating}
                    placeholder={t('amount')}
                    className={classnames(
                      'input-box',
                      isCalculating ? 'input-disabled' : '',
                    )}
                    value={values.price}
                    maxLength={10}
                    onChange={(event: any) => {
                      if (!isCalculating) {
                        handleChange(event)
                        setInputAmount(event.target.value)
                        setDelayedLaunching(false)
                      }
                    }}
                    onBlur={handleBlur}
                  />
                </div>
                <NftSummary
                  estimatedValue={fetchSinglePlayerStatsData[0]?.matic} //{'0.0010398173320689333'
                  totalValue={totalSum}
                  inProgress={isCalculating}
                  // usdRate={fetchSinglePlayerStatsRateData?.rate || 0}
                  usdRate={fetchSinglePlayerStatsData[0]?.exchangeRateUSD?.rate}
                  initCallback={getCallback}
                  stopCalculating={stopCalculation}
                  usdTotalCallback={fetchLiveTotalUsd}
                />
                {isLoading ? (
                  <div className="spinner-wrapper purchase-loader">
                    <Spinner
                      className="purchase-spinner"
                      spinnerStatus={isLoading}
                      title={t('awaiting Confirmation')}
                    />
                  </div>
                ) : null}
                <div
                  className={classNames(
                    'purchase-submit-wrapper',
                    'btn-purchase',
                    isLoading ? 'hidden' : '',
                  )}
                >
                  {/* <ResponseAlert status={response} />
                  {txHash && (
                    <a
                      className="tx-link"
                      href={'https://mumbai.polygonscan.com/tx/' + txHash}
                      target="_blank"
                    >
                      {t('show Transaction')}
                    </a>
                  )} */}
                  {/* <div style={{ height: '75px' }}></div> */}
                  <PurchaseButton
                    disabled={isLoading}
                    isInactive={
                      isFundsInsufficient || fetchSinglePlayerStatsError
                    }
                    title={buttonTitle}
                    onPress={handleSubmit}
                    // className={classnames(
                    //   isCalculating && !stopCalculation
                    //     ? 'purchase-btn-inactive'
                    //     : '',
                    // )}
                    className={classnames(
                      fetchSinglePlayerStatsError
                        ? 'purchase-btn-inactive'
                        : '',
                    )}
                  />
                  {fetchSinglePlayerStatsError && (
                    <div className="input-feedback text-center">
                      {fetchSinglePlayerStatsError}
                    </div>
                  )}
                  <div
                    className={classnames(
                      'supported-methods-wrapper',
                      isMobile() ? 'mb-20' : '',
                    )}
                  >
                    <div className="methods-heading">
                      {t('accepted payment methods')}
                    </div>
                    <div className="methods-wrapper">
                      <TooltipLabel title="MATIC">
                        <img src={maticIcon} alt="" className="method-matic" />
                      </TooltipLabel>
                      <TooltipLabel title="Visa">
                        <img src={visaIcon} alt="" className="method-visa" />
                      </TooltipLabel>
                      <TooltipLabel title="Master Card">
                        <img
                          src={masterCardIcon}
                          alt=""
                          className="method-mc"
                        />
                      </TooltipLabel>
                    </div>
                  </div>
                </div>
              </form>
            )
          }}
        </Formik>
      </div>
    </div>
  )
}

export default BuyNftForm
