import React, { useEffect, useState, useContext } from 'react'
import { Formik } from 'formik'
import Web3 from 'web3'
import { ethers } from 'ethers'
import * as Yup from 'yup'
import classnames from 'classnames'
import SubmitButton from '@components/SubmitButton'
import FormInput from '@components/Form/FormInput'
import { Input } from '@components/Form'
import { useDispatch, useSelector } from 'react-redux'
import {
  getWalletDetails,
  sendMatics,
} from '@root/apis/onboarding/authenticationSlice'
import { RootState } from '@root/store/rootReducers'
import { useTranslation } from 'react-i18next'
import { ConnectContext } from '@root/WalletConnectProvider'
import { isMobile, truncateDecimals } from '@utils/helpers'
interface Props {
  onSubmit: (v?: any) => void
  onClose: () => void
  assetType?: string
  playerData?: any
}

const initialValues = {
  matic: '',
  address: '',
}

const Send: React.FC<Props> = ({
  onSubmit,
  onClose,
  playerData = null,
  assetType = 'matic',
}) => {
  const loginInfo = localStorage.getItem('loginInfo')
  const authenticationData = useSelector(
    (state: RootState) => state.authentication,
  )
  const [availableBalance, setAvailableBalance] = useState<any>('0.00')
  const [inputAmount, setInputAmount] = useState('')
  const [isInvalidEthAddress, setInvalidEthAddress] = useState<boolean>(false)
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const { sendWithWallet, getBalance } = useContext(ConnectContext)
  const {
    loader,
    passphraseLoader,
    userWalletData: { USDBalance, address, balance, exchangeRateData },
  } = authenticationData
  console.log('AUTHSEND--', { authenticationData })
  useEffect(() => {
    // setAvailableBalance(parseFloat(balance).toFixed(2))
    setAvailableBalance(balance)
    console.log('autosend---', balance)
  }, [balance])

  useEffect(() => {
    // dispatch(getWalletDetails())
    console.log({ playerData })
  }, [playerData])

  useEffect(() => {
    const _getBalance = async () => {
      if (loginInfo) {
        await getBalance()
      }
      const matic = localStorage.getItem('balance')
      const usd = (
        parseFloat(matic || '') * exchangeRateData?.rate ?? 0
      ).toFixed(3)
      console.log('sboop---', matic)
      if (matic) {
        setAvailableBalance(matic)
      }
    }
    _getBalance()
  }, [])

  const submitForm = (values: any) => {
    console.log({ values })
    const reqParams = {
      to_address: values.address,
      amount: parseFloat(values.matic),
    }
    let isValidAddress = false
    try {
      const isAddress = ethers.utils.getAddress(values.address)
      if (isAddress) {
        isValidAddress = true
      }
    } catch (err: any) {
      console.log('ERRORVALID:::' + JSON.stringify(err))
      console.log('inputAdd---', values.address)
      if (err?.reason === 'invalid address') {
        isValidAddress = false
      }
    }
    if (isValidAddress) {
      onSubmit(reqParams)
    } else {
      setInvalidEthAddress(true)
    }
    // dispatch(sendMatics(reqParams))
  }

  const isFundsInsufficient =
    parseFloat(availableBalance) < parseFloat(inputAmount) ||
    parseFloat(availableBalance) === 0

  const isTokensInsufficient = playerData
    ? parseFloat(playerData.balance) < parseFloat(inputAmount) ||
      parseFloat(playerData.balance) === 0
    : null

  return (
    <section className="wallet-container">
      <Formik
        enableReinitialize={true}
        initialValues={initialValues}
        // onSubmit={submitForm}
        onSubmit={(values, { resetForm }) => {
          submitForm(values)
          resetForm()
        }}
        validationSchema={Yup.object().shape({
          matic: Yup.string().required('matic amount required'),
          address: Yup.string().required('receiver address required'),
        })}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            isValid,
            dirty,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
          } = props
          return (
            <form autoComplete="off" onSubmit={handleSubmit}>
              {playerData ? (
                <div
                  className={classnames(
                    'nft-item pointer',
                    'player-data',
                    isMobile() ? 'wallet-player-coin' : '',
                  )}
                >
                  <div className="nft-image-section">
                    <div className="image-border pc-avatar-container">
                      <img
                        loading="lazy"
                        src={playerData?.playerpicture}
                        alt=""
                        className="nft-image"
                      />
                    </div>
                  </div>
                  <div className="nft-name-section pc-name-section">
                    <div className="nft-name">{playerData.name}</div>
                  </div>
                </div>
              ) : null}
              <div className="field-wrapper m-0">
                {playerData ? (
                  <label>
                    <b>{t('amount')}</b>
                  </label>
                ) : (
                  <label>
                    <b>{'MATIC ' + t('sending') + ':'}</b>
                  </label>
                )}
                <Input
                  id="matic"
                  name="matic"
                  type="text"
                  placeholder={t('amount')}
                  className="input-box matic-input"
                  value={values.matic}
                  maxLength={12}
                  onChange={(event: any) => {
                    handleChange(event)
                    setInputAmount(event.target.value)
                  }}
                  onBlur={handleBlur}
                />
                {errors.matic && touched.matic && (
                  <div className="input-feedback">{errors.matic}</div>
                )}
              </div>
              <div className="text-wrapper send-max-coins">
                {assetType === 'playerCoin' ? (
                  <div>{playerData?.name + ' ' + t('coins')}:</div>
                ) : (
                  <div>{t('maximum coins to send')}:</div>
                )}
                {assetType === 'playerCoin' ? (
                  <div
                    className="coins-available"
                    onClick={() => setFieldValue('matic', playerData.balance)}
                  >
                    {!isNaN(playerData.balance) && playerData.balance !== null
                      ? ' ' + truncateDecimals(playerData.balance, 4)
                      : '0.00'}{' '}
                  </div>
                ) : (
                  <div
                    className="coins-available"
                    onClick={() => setFieldValue('matic', availableBalance)}
                  >
                    {!isNaN(availableBalance) && availableBalance !== null
                      ? ' ' + parseFloat(availableBalance).toFixed(5)
                      : '0.00'}{' '}
                    MATIC
                  </div>
                )}
              </div>
              <div className="field-wrapper">
                <label>
                  <b>{t('enter Receiver Address')}:</b>
                </label>
                <FormInput
                  id="address"
                  type="text"
                  placeholder={t('receiver Address')}
                  value={values.address}
                  name="address"
                  handleChange={handleChange}
                  onBlur={handleBlur}
                />
                {errors.address && touched.address && (
                  <div className="input-feedback">{errors.address}</div>
                )}
              </div>
              <div className="space-block mt-20">
                {playerData ? (
                  <>
                    {isTokensInsufficient && (
                      <div className="input-feedback text-center fullwidth mt-40">
                        {t('insufficient Balance to make transaction')}
                      </div>
                    )}
                  </>
                ) : (
                  <>
                    {isFundsInsufficient && (
                      <div className="input-feedback text-center fullwidth mt-40">
                        {t('insufficient Balance to make transaction')}
                      </div>
                    )}
                  </>
                )}
                {isInvalidEthAddress && (
                  <div className="input-feedback text-center fullwidth mt-40">
                    {t('invalid etherium address')}
                  </div>
                )}
              </div>
              <div className="bottom-button-box">
                {!loginInfo ? (
                  <div className="send-divider mb-40">
                    {playerData ? (
                      <SubmitButton
                        isDisabled={isTokensInsufficient || !(isValid && dirty)}
                        title={t('confirm')}
                        className="m-0auto"
                        onPress={handleSubmit}
                      />
                    ) : (
                      <SubmitButton
                        isDisabled={isFundsInsufficient || !(isValid && dirty)}
                        title={t('confirm')}
                        className="m-0auto"
                        onPress={handleSubmit}
                      />
                    )}
                    <div
                      className="form-submit-btn btn-disabled mt-20 m-0auto"
                      onClick={onClose}
                    >
                      {t('cancel')}
                    </div>
                  </div>
                ) : (
                  <>
                    <div
                      className={classnames(
                        'send-divider mb-40',
                        passphraseLoader ? 'hidden' : '',
                      )}
                    >
                      <SubmitButton
                        isDisabled={isFundsInsufficient}
                        title={t('confirm')}
                        className="m-0auto"
                        onPress={handleSubmit}
                      />
                      <div
                        className="form-submit-btn btn-disabled mt-20 m-0auto"
                        onClick={onClose}
                      >
                        {t('cancel')}
                      </div>
                    </div>
                    <div
                      className={classnames(
                        'passphrase-progress-wrapper mt-40',
                        passphraseLoader ? 'show' : 'hidden',
                      )}
                    >
                      <div className="spinner__circle">
                        <div className="spinner__circle-gradient"></div>
                        <div className="spinner__circle-inner"></div>
                      </div>
                    </div>
                  </>
                )}
              </div>
            </form>
          )
        }}
      </Formik>
    </section>
  )
}

export default Send
