import React, { useState, useEffect, useContext } from 'react'
import { PlayerCardData as PlayerItems, PLAYER_STATUS } from '@root/constants'
import SearchBar from '@components/SearchBar'
import PlayerItem from './PlayerItem'
import DialogBox from '@components/DialogBox'
import StakedForm from './StakedForm'
import { isMobile } from '@utils/helpers'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import {
  fetchPlayersBalance,
  // fetchPlayersStats,
} from '@root/apis/playerCoins/playerCoinsSlice'
import { RootState } from '@root/store/rootReducers'
import { useTranslation } from 'react-i18next'
import classNames from 'classnames'
import { getUsdRate } from '@root/apis/purchase/purchaseSlice'
import {
  fetchPlayerCoinStats,
  fetchPlayersStats,
} from '@root/apis/playerStats/playerStatsSlice'
import BottomPopup from '@components/BottomPopup'
import SendAsset from '../MyWallet/SendAsset'
import { ConnectContext } from '@root/WalletConnectProvider'
import { sendMaticsReset } from '@root/apis/onboarding/authenticationSlice'
import { ethers } from 'ethers'

let intervalId: any = null
interface PlayerOffset {
  start: number
  end: number
}

const PlayerCoins: React.FC = () => {
  const [showFormPopup, setShowFormPopup] = useState(false)
  const [hasCompleted, setHasCompleted] = useState(false)
  const [optedId, setOptedId] = useState(NaN)
  const [traverseTokens, setTraverseTokens] = useState([])
  const [isSendCoin, setSendCoin] = useState<boolean>(false)
  const [isLoadingMatic, setLoadingMatic] = useState(false)

  const [web3CallInProgress, setWeb3CallInProgress] = useState(false)
  const [isDepositSelected, setDepositSelected] = useState(false)
  const [transactionData, setTransactionData] = useState(null)
  const [isSecretAcquired, setAquireSecret] = useState(false)
  const [isExportKeyOpted, setExportKeyOpted] = useState(false)
  const [txnHash, setTxnHash] = useState('')
  const [txnError, setTxnError] = useState('')
  const [sendData, setSendData] = useState<any>(null)
  const [stateBalance, setBalance] = useState<any>('0.00')
  const [usdBalance, setUsdBalance] = useState<any>()
  const { sendWithWallet, getBalance } = useContext(ConnectContext)
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const loginInfo = localStorage.getItem('loginInfo')
  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const playerStatsData = useSelector((state: RootState) => state.playerstats)
  const {
    fetchPlayerCoinStatsError,
    fetchPlayerCoinStatsData,
    fetchPlayerCoinStatsRateData,
  } = playerStatsData
  const { t } = useTranslation()
  const {
    fetchBalancePlayersProgress,
    fetchBalancePlayersSuccess,
    fetchBalancePlayersData,
    fetchBalancePlayersError,
    isStakingOnlySelected,
  } = playerCoinData
  const purchaseTransactionData = useSelector(
    (state: RootState) => state.purchases,
  )
  const authenticationData = useSelector(
    (state: RootState) => state.authentication,
  )
  const {
    loader,
    passphraseLoader,
    userWalletData: { USDBalance, address, balance, exchangeRateData },
  } = authenticationData
  const { usdRate } = purchaseTransactionData
  const { token = [], NFT = [], matic = [] } = fetchBalancePlayersData

  const handleClose = (event: any) => {
    event.stopPropagation()
    setShowFormPopup(false)
  }

  const handleStake = (item: any) => {
    if (isMobile()) {
      navigate('/staked')
    } else {
      setShowFormPopup(true)
    }
  }
  const arrs: any = []
  const handleSearch = (value: string) => {
    const traverseTemp: any = []
    let i: number
    const filter = value?.toUpperCase()

    for (i = 0; i < token?.length; i++) {
      if (token[i]?.name?.toUpperCase()?.indexOf(filter) > -1) {
        traverseTemp?.push(token[i])
      } else {
        traverseTemp?.filter((item: any) => item?.name === token[i]?.name)
      }
    }
    setTraverseTokens(traverseTemp)
  }

  useEffect(() => {
    if (fetchBalancePlayersProgress) {
      // setTraverseTokens([])
    }
  }, [fetchBalancePlayersProgress])

  const handleGetPriceStats = () => {
    console.log('myTokens--', token)
    console.log({ fetchPlayerCoinStatsData })
    // const playersSet: number[] = token
    //   .filter((player: { playerstatusid: { id: number } }) => {
    //     return player?.playerstatusid?.id > 2
    //     // if (player?.playerstatusid?.id > 2) {
    //     //   return player?.contract
    //     // }
    //   })
    //   .map((item: any) => item?.contract)
    // if (
    //   playersBannerDataTemp[j]?.player &&
    //   fetchPlayerStatsData[i]?.player &&
    //   ethers.utils.getAddress(playersBannerDataTemp[j]?.player) ===
    //     ethers.utils.getAddress(fetchPlayerStatsData[i]?.player)
    // ) {
    //   playersBannerDataTemp[j] = {
    //     ...playersBannerDataTemp[j],
    //     ...fetchPlayerStatsData[i],
    //   }
    // }
    const playersSet: number[] = token.map((item: any) => item?.contract)
    console.log({ playersSet })
    if (playersSet?.length > 0) {
      dispatch(fetchPlayerCoinStats(playersSet))
    }
  }

  useEffect(() => {
    // const reqParams = {
    //   address: loginInfo || address,
    // }
    // dispatch(fetchPlayersBalance(reqParams))
    dispatch(getUsdRate())
    // intervalId = setInterval(() => {
    //   dispatch(getUsdRate())
    // }, 5000)
    return () => clearInterval(intervalId)
  }, [])

  useEffect(() => {
    const reqParams = {
      address: loginInfo || address,
    }
    clearInterval(intervalId)
    console.log('fetching balance 1st time')
    setTraverseTokens([])
    dispatch(fetchPlayersBalance(reqParams))
    console.log({ isStakingOnlySelected })
  }, [isStakingOnlySelected])

  useEffect(() => {
    if (token?.length > 0) {
      if (!hasCompleted) {
        handleGetPriceStats()
        setHasCompleted(true)
      }
      intervalId = setInterval(() => {
        handleGetPriceStats()
      }, 10000)
      console.log('loadertoken--', { token })
      setTraverseTokens(token)
    }
  }, [token])

  const onMoreClick = (checked: boolean, id: number) => {
    if (checked) {
      setOptedId(id)
    } else {
      setOptedId(NaN)
    }
  }
  // ethers.utils.getAddress(playersBannerDataTemp[j]?.player) ===
  //     ethers.utils.getAddress(fetchPlayerStatsData[i]?.player)
  const getStatData = (playerContract: any) => {
    let index = null
    if (fetchPlayerCoinStatsData && fetchPlayerCoinStatsData.length > 0) {
      console.log('GSDU--', { playerContract, fetchPlayerCoinStatsData })
      index = fetchPlayerCoinStatsData?.findIndex((item: any) => {
        if (item.player && playerContract) {
          return (
            ethers.utils.getAddress(item?.player) ===
            ethers.utils.getAddress(playerContract)
          )
        } else {
          return -1
        }
      })
      if (index > -1) {
        return {
          coin_issued: fetchPlayerCoinStatsData[index]?.coin_issued,
          matic: fetchPlayerCoinStatsData[index]?.matic,
          usd_rate: fetchPlayerCoinStatsData[index]?.exchangeRateUSD?.rate,
        }
      } else {
        return {
          coin_issued: fetchPlayerCoinStatsData[index]?.coin_issued,
          matic: fetchPlayerCoinStatsData[index]?.matic,
          usd_rate: fetchPlayerCoinStatsRateData[index]?.exchangeRateUSD?.rate,
        }
      }
    }

    // return {
    //   coin_issued: fetchPlayerCoinStatsData[index]?.coin_issued,
    //   matic: fetchPlayerCoinStatsData[index]?.matic,
    //   usd_rate: fetchPlayerCoinStatsRateData[index]?.exchangeRateUSD?.rate,
    // }
    return {
      coin_issued: '000',
      matic: '000',
      usd_rate: '000',
    }
  }

  const handleContainerClick = (evt: any) => {
    evt.preventDefault()
    setOptedId(NaN)
  }

  const handlePlayerCallback = (val: string, data: any) => {
    if (val === 'send') {
      console.log('boor')
      setSendCoin(true)
      setSendData(data)
    }
  }

  const handleCloseSecret = () => {
    setAquireSecret(false)
    setSendCoin(false)
    setDepositSelected(false)
    setSendCoin(false)
  }

  const handleCloseBottomPopup = () => {
    console.log('JCBP')
    setLoadingMatic(false)
    setSendCoin(false)
    setTxnHash('')
    setTxnError('')
    setWeb3CallInProgress(false)
  }

  const handleFetchTransactionData = (data: any) => {
    console.log('HFTDRUN', data)
    setTxnHash('')
    setTxnError('')
    setWeb3CallInProgress(true)
    setLoadingMatic(true)
    if (loginInfo) {
      // dispatch(sendMaticsMetamask())
      sendWithWallet(
        data.to_address,
        data.amount,
        txnHash => {
          console.log('jjj---', txnHash)
          setLoadingMatic(false)
          setTxnHash(txnHash)
        },
        error => {
          console.log('ssss---', error)
          setLoadingMatic(false)
          setTxnError(error.message)
        },
      )
      // setTimeout(() => {
      //   handleCloseSecret()
      //   dispatch(sendMaticsReset())
      // }, 2000)
    } else {
      setTransactionData(data)
      setAquireSecret(true)
      dispatch(sendMaticsReset())
    }
  }

  return (
    <>
      <SearchBar
        isSwitchEnabled={true}
        mode="wallet"
        onEdit={handleSearch}
        onClose={() => handleSearch('')}
      />
      {/* <DialogBox
        isOpen={showFormPopup}
        onClose={handleClose}
        contentClass=""
        closeBtnClass="close-purchase"
      >
        <StakedForm />
      </DialogBox> */}
      <BottomPopup mode="wallet drafting-players-loader" isOpen={showFormPopup}>
        <StakedForm />
      </BottomPopup>

      <SendAsset
        playerData={sendData}
        isOpen={isSendCoin}
        onSend={handleFetchTransactionData}
        onCloseSend={handleCloseSecret}
        onClose={handleCloseBottomPopup}
        txnError={txnError}
        txnHash={txnHash}
        isLoadingMatic={isLoadingMatic}
      />
      <div
        className={classNames(
          'dlg-content border-top m-0 fixed-content',
          // fetchBalancePlayersProgress ? 'fixed-content' : '',
        )}
        style={{ height: isMobile() ? '649px' : '83%' }}
        onClick={handleContainerClick}
      >
        {/* {PlayerItems.map((item, index) => (
          <PlayerItem
            item={item}
            key={index}
            index={index}
            handleStake={handleStake}
          />
        ))} */}
        {traverseTokens?.length > 0 ? (
          traverseTokens?.map((item: any, index: number) => (
            <PlayerItem
              isSendOpen={isSendCoin}
              item={item}
              key={index}
              index={index}
              statData={getStatData(item?.contract)}
              handleStake={() => handleStake(item)}
              usdRate={usdRate}
              handleMoreClick={checked => onMoreClick(checked, index)}
              isOpted={optedId === index}
              playerItemcallBack={(value: string) =>
                handlePlayerCallback(value, item)
              }
            />
          ))
        ) : (
          <div
            className="checkout-loader-wrapper"
            style={{ height: '100%', overflowX: 'hidden' }}
          >
            {((fetchBalancePlayersProgress && !fetchBalancePlayersSuccess) ||
              !fetchPlayerCoinStatsData?.length) &&
            !fetchBalancePlayersError ? (
              <>
                {fetchBalancePlayersSuccess && traverseTokens?.length === 0 ? (
                  <div className="drafts-no-action-container no-new-draft">
                    <div>{t('no results found')}</div>
                  </div>
                ) : (
                  <div className="loading-spinner">
                    <div className="spinner__circle">
                      <div className="spinner__circle-gradient"></div>
                      <div className="spinner__circle-inner"></div>
                    </div>
                  </div>
                )}
              </>
            ) : (
              <div className="drafts-no-action-container no-new-draft">
                <div>{t('no results found')}</div>
              </div>
            )}
          </div>
        )}
      </div>
    </>
  )
}

export default PlayerCoins
