/* eslint-disable @typescript-eslint/no-non-null-assertion */
import React, { useEffect } from 'react'
import { AppLayout } from '@components/index'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { RootState } from '@root/store/rootReducers'
import { isMobile } from '@utils/helpers'
import {
  showSignupForm,
  showWalletForm,
} from '@root/apis/onboarding/authenticationSlice'
import DialogBox from '@components/DialogBox'
import OnboardingForm from '@pages/Onboarding/OnboardingForm'
import WalletForm from '@pages/Wallet/WalletForm'
import { useTranslation } from 'react-i18next'
import '@assets/css/pages/LaunchCoin.css'
import TagManager from 'react-gtm-module'
import { DummyCardData, tagManagerArgs } from '@root/constants'
import '@assets/css/components/PlayerCard.css'
import maticIcon from '@assets/images/matic-token-icon.png'
import visaIcon from '@assets/images/visa-white.png'
import masterCardIcon from '@assets/images/mastercard-large.png'
import TooltipLabel from '@components/TooltipLabel'
import SubmitButton from '@components/SubmitButton'

const LaunchCoin: React.FC = () => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const { t } = useTranslation()
  const loginInfo = localStorage.getItem('loginInfo')
  const loginId = localStorage.getItem('loginId')

  const handleSubmit = async () => {
    handleClose()
  }

  const showSignupPopup = useSelector(
    (state: RootState) => state.authentication.isSignupFormVisible,
  )

  const showWalletPopup = useSelector(
    (state: RootState) => state.authentication.isWalletFormVisible,
  )

  useEffect(() => {
    window.scrollTo(0, 0)
    TagManager.initialize(tagManagerArgs)
    document.querySelector('title')!.innerText = 'Launch your own player coin'
    document
      .querySelector("meta[name='description']")!
      .setAttribute(
        'content',
        'Launch your football player coin now and become a meCarreira PRO',
      )
    document.body.style.backgroundColor = '#171923'
    return () => {
      document.body.style.backgroundColor = '#222435'
    }
  }, [])

  const handleClose = () => {
    if (isMobile()) {
      navigate('/')
    } else {
      if (showSignupPopup) {
        dispatch(showSignupForm())
      } else if (showWalletPopup) {
        dispatch(showWalletForm({}))
      }
    }
  }

  const handleStart = () => {
    if (!loginInfo && !loginId) {
      localStorage.setItem('routeAfterLogin', '/launch-your-coin')
      if (isMobile()) {
        navigate('/signup')
      } else {
        dispatch(showSignupForm())
      }
      return
    }
    localStorage.setItem('previousPath', '/launch-your-coin')
    navigate('/player-dashboard')
  }

  return (
    <AppLayout headerClass="home" footerStatus="footer-status">
      {showSignupPopup && (
        <DialogBox isOpen={showSignupPopup} onClose={handleClose}>
          <OnboardingForm onSubmit={handleSubmit} onClose={handleClose} />
        </DialogBox>
      )}
      {showWalletPopup && (
        <DialogBox isOpen={showWalletPopup} onClose={handleClose}>
          <WalletForm />
        </DialogBox>
      )}
      <div className="launch-coin-container">
        <div className="heading-container">
          <div>
            <div className="heading-title">{t('be Seen')}</div>
            <div className="heading-title">{t('be Traded')}</div>
            <div className="heading-title">{t('be Valuable')}</div>
          </div>
          <div className="heading-desc">
            <div>{t('your_player_coin_is')}</div>
            <br />
            <div>{t('it_doesnt_matter')}</div>
            <br />
            <div>{t('you will')}</div>
            <br />
            <div> - {t('get Attention of Agents, Scouts & PROs')}</div>
            <div> - {t('be able to earn money with your hobby')}</div>
            <div> - {t('become a meCarreira PRO Player')}</div>
            <div> - {t('push your career and market value')}</div>
          </div>
        </div>
        <div className="card-container">
          <div className="card coming-soon-wrapper">
            <div className="inside-card">
              <div className="matic-wrapper">
                <div className="number-color matic-value matic-figure">
                  0.34
                </div>
                <TooltipLabel title="MATIC">
                  <img src={maticIcon} alt="" />
                </TooltipLabel>
              </div>
              <div className="matic-wrapper">
                <div className="number-color pt-5 usd-price">$0.28</div>
              </div>
            </div>
            <div className="time capitalize m-0">&nbsp;</div>
            <div>
              <div className="img">
                <img
                  src={DummyCardData.img.default}
                  alt=""
                  className="img-radius cache-img"
                />
                <div className="player-img-desc">{t('your Picture')}</div>
              </div>
              <div className="name player-title-name">{t('your Name')}</div>
              <div className="link">
                <div className="link-icon"></div>
              </div>
              <div className="divide mt-10"></div>
              <div className="changed-price">
                <div>{t('price change 24h')}</div>
                <div className="number-color profit">+4.83%</div>
              </div>
              <div className="coin-issued">
                <div>{t('coin issued')}</div>
                <div className="number-color">71456</div>
              </div>
              <div className="holders">
                <div>{t('holders')}</div>
                <div className="number-color">64</div>
              </div>
            </div>
            <div className="bottom-part">
              <div className="buy">{t('buy')}</div>
              <div className="sell">{t('sell')}</div>
            </div>
          </div>
        </div>
        <div className="heading-container">
          <div className="heading-desc">
            <div>{t('the_player_coin_is')}</div>
          </div>
          <div className="footer-icons">
            <img
              className="card-logo"
              src={visaIcon}
              style={{ height: '27px' }}
              alt=""
            />
            <img
              className="card-logo"
              src={masterCardIcon}
              style={{ height: '43px' }}
              alt=""
            />
            <img
              className="card-logo"
              src={maticIcon}
              style={{ height: '50px' }}
              alt=""
            />
          </div>
        </div>
        <div className="button-container">
          <SubmitButton
            isDisabled={false}
            title={t('lets start')}
            onPress={handleStart}
          />
        </div>
      </div>
    </AppLayout>
  )
}

export default LaunchCoin
