import React, { useState, useRef, useEffect } from 'react'
import MoreVertIcon from '@mui/icons-material/MoreVert'
import { NumberFormat } from '@utils/NumberFormat'
import { useDispatch } from 'react-redux'
import { setPurchasePlayerId } from '@root/apis/purchase/purchaseSlice'
import { useTranslation } from 'react-i18next'
import { isMobile, toFixedTrunc, truncateDecimals } from '@utils/helpers'
import PopperMenu from '@components/PopperMenu'
import classNames from 'classnames'
import HoverMenu from '@components/HoverMenu'

interface playerItem {
  img: string
  name: string
  price: number
  playerpicture: string
  stakingbalance: number
  balance: number
  status?: any
  isShown?: boolean
  playerstatusid?: any
}

interface Props {
  index?: number
  item: playerItem
  handleStake: () => void
  handleMoreClick: (value: boolean) => void
  isOpted?: boolean
  usdRate?: number | string
  statData?: any
  isSendOpen?: boolean
  playerItemcallBack?: (val: string) => void
}

const PlayerItem: React.FC<Props> = props => {
  const {
    index,
    item,
    handleStake,
    handleMoreClick,
    isSendOpen,
    isOpted = false,
    usdRate = 0.0,
    statData = { coin_issued: '', matic: '' },
    playerItemcallBack,
  } = props
  const dispatch = useDispatch()
  // const [isOpted, setIsOpted] = useState<boolean>(false)
  const { t } = useTranslation()

  const [clicked, setClicked] = useState(false)

  const prevStatRef: any = useRef()
  useEffect(() => {
    prevStatRef.current = statData
  }, [statData])

  useEffect(() => {
    console.log({ prevStatRef })
  }, [prevStatRef])

  useEffect(() => {
    console.log('barb--', { item })
  }, [item])

  const handleClick = (evt: any) => {
    console.log('clickedevt--', evt)
    if (
      !['more-icon', 'hover-modal-menuitem'].includes(evt.target.id) &&
      !isSendOpen
    ) {
      if (!clicked) setClicked(true)
      handleMoreClick(false)
    }
  }

  const getUsdPrice = () => {
    // const prevCoinIssued = prevStatRef?.current?.coin_issued

    // const prevMatics = prevStatRef?.current?.matic
    // const prevUsd = prevStatRef?.current?.usd_rate
    // const { coin_issued, matic, usd_rate } = statData
    // let usdPrice = 0
    // if (coin_issued && matic && usd_rate) {
    //   usdPrice = (matic * item.balance) / usd_rate
    // } else {
    //   usdPrice = (prevMatics * item.balance) / prevUsd
    // }
    // if (!isNaN(usdPrice)) {
    //   return truncateDecimals(usdPrice, 4)
    // }
    // return '00.00'
    const prevMatics = prevStatRef?.current?.matic
    const prevUsd = prevStatRef?.current?.usd_rate
    let totalUsd = null
    const { coin_issued, matic, usd_rate } = statData
    if (coin_issued && matic && usd_rate) {
      // usdPrice = (matic * item.balance) / usd_rate
      const totalValue = parseFloat(matic) * item.balance
      totalUsd = totalValue * usd_rate
    } else {
      // usdPrice = (prevMatics * item.balance) / prevUsd
      const totalValue = parseFloat(prevMatics) * item.balance
      totalUsd = totalValue * prevUsd
    }
    // const totalValue = parseFloat(matic) * item.balance
    // const totalUsd = totalValue * usd_rate
    return totalUsd.toFixed(3)
  }

  const handleStakeClick = () => {
    // dispatch(setPurchasePlayerId(item))
    setClicked(false)
    handleStake()
  }

  const onClickMore = (evt: any) => {
    evt.stopPropagation()
    if (isOpted) {
      handleMoreClick(false)
    } else {
      handleMoreClick(true)
    }
  }

  const onSendOpt = (val: any) => {
    console.log('OSO--', val)
    playerItemcallBack('send')
  }

  return (
    <div
      key={index}
      className={classNames(
        'nft-item pointer',
        isMobile() ? 'wallet-player-coin' : '',
      )}
      onClick={handleClick}
    >
      <div className="nft-image-section">
        {/* <MoreVertIcon onClick={onClickMore} /> */}
        {/* <PopperMenu
          isOpen={isOpted}
          isDisabled={item?.playerstatusid?.id < 5}
          sendCallback={onSendOpt}
        /> */}
        <HoverMenu options={['Send']} onItemPress={onSendOpt} />
        <div className="image-border pc-avatar-container">
          <img
            loading="lazy"
            src={item?.playerpicture}
            alt=""
            className="nft-image"
          />
        </div>
      </div>
      {clicked ? (
        <div className="nft-name-section nft-first-name-section">
          <div
            className="nft-name select_nft_stake bottom-border"
            onClick={handleStakeClick}
          >
            {t('stake') + '/' + t('unstake')}
          </div>
          <div
            className="select_nft_cancel bottom-border"
            onClick={() => setClicked(false)}
          >
            {t('cancel')}
          </div>
        </div>
      ) : (
        <div className="nft-name-section pc-name-section">
          <div className="nft-name">{item.name}</div>
          <div>
            <div style={{ width: '120px' }}>
              {item?.stakingbalance && item?.stakingbalance > 0 ? (
                <span className="green-color">
                  {/* truncateDecimals(currency, 4) */}
                  {truncateDecimals(item?.stakingbalance, 2)} / {''}
                </span>
              ) : null}
              {/* {NumberFormat(item?.balance)} */}
              {truncateDecimals(item?.balance, 5)}
            </div>
            <div className="nft-price">${getUsdPrice()}</div>
          </div>
        </div>
      )}
    </div>
  )
}

export default PlayerItem
