import React, { useState, useEffect, useContext } from 'react'
import { useTranslation } from 'react-i18next'
import classnames from 'classnames'
import SubmitButton from '@components/SubmitButton'
import IosShareIcon from '@mui/icons-material/IosShare'
import '@assets/css/pages/Wallet.css'
import BottomPopup from '@components/BottomPopup'
import Send from './Send'
import PassPhrase from './PassPhrase'
import Deposit from './Deposit'
import { isMobile } from '@utils/helpers'
import { useDispatch, useSelector } from 'react-redux'
import {
  exportKeyReset,
  getWalletAddress,
  getWalletDetails,
  sendMaticsMetamask,
  sendMaticsReset,
} from '@root/apis/onboarding/authenticationSlice'
import { RootState } from '@root/store/rootReducers'
import ExportPrivateKey from './ExportPrivateKey'
import { ConnectContext } from '@root/WalletConnectProvider'
import ResponseAlert from '@components/ResponseAlert'
import MetamaskIcon from '@assets/icons/icon/metamask.svg'
import CoinbaseIcon from '@assets/icons/icon/coinbase.svg'
import SendAsset from './SendAsset'
import CheckCircleOutlinedIcon from '@mui/icons-material/CheckCircleOutlined'
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined'
interface Props {
  onSubmit: any
}

const MyWallet: React.FC<Props> = ({ onSubmit }) => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const loginInfo = localStorage.getItem('loginInfo')
  const loginId = localStorage.getItem('loginId')
  const walletType = localStorage.getItem('wallet')
  const [fixedRate, setFixedRate] = useState('')
  const [isSendMatic, setSendMatic] = useState(false)
  const [web3CallInProgress, setWeb3CallInProgress] = useState(false)
  const [isDepositSelected, setDepositSelected] = useState(false)
  const [transactionData, setTransactionData] = useState(null)
  const [isSecretAcquired, setAquireSecret] = useState(false)
  const [isExportKeyOpted, setExportKeyOpted] = useState(false)
  const [txnHash, setTxnHash] = useState('')
  const [txnError, setTxnError] = useState('')
  const [isLoadingMatic, setIsLoadingMatic] = useState(false)
  const [stateBalance, setBalance] = useState<any>('0.00')
  const [usdBalance, setUsdBalance] = useState<any>()
  const { sendWithWallet, getBalance } = useContext(ConnectContext)

  const authenticationData = useSelector(
    (state: RootState) => state.authentication,
  )

  const {
    loader,
    userWalletData: { address, USDBalance, exchangeRateData, balance },
  } = authenticationData

  useEffect(() => {
    const _getBalance = async () => {
      if (loginInfo) {
        await getBalance()
      }
      const matic = localStorage.getItem('balance')
      const usd = (
        parseFloat(matic || '') * exchangeRateData?.rate ?? 0
      ).toFixed(3)
      setBalance(matic)
      setUsdBalance(usd)
    }
    _getBalance()
  }, [])

  const handleCloseBottomPopup = () => {
    console.log('JCBP')
    setIsLoadingMatic(false)
    setTxnHash('')
    setTxnError('')
    setWeb3CallInProgress(false)
  }

  const handleRefreshBalance = async (event: any) => {
    event.preventDefault()
    if (loginInfo) {
      dispatch(getWalletAddress(loginInfo))
      await getBalance()
    } else {
      dispatch(getWalletDetails())
    }
    const matic = localStorage.getItem('balance')
    const usd = (parseFloat(matic || '') * exchangeRateData.rate).toFixed(3)
    setBalance(matic)
    setUsdBalance(usd)
  }

  const handleSendMatic = () => {
    setTxnError('')
    setTxnHash('')
    setSendMatic(true)
  }

  const handleFetchTransactionData = (data: any) => {
    console.log('HFTDRUNSS', data)
    setIsLoadingMatic(true)
    // setTxnHash('')
    // setTxnError('')
    console.log('hawwa')
    if (loginInfo) {
      console.log('brow--', walletType)
      setWeb3CallInProgress(true)
      dispatch(sendMaticsMetamask())
      sendWithWallet(
        data.to_address,
        data.amount,
        txnHash => {
          console.log('jjj---', txnHash)
          setIsLoadingMatic(false)
          setTxnHash(txnHash)
        },
        error => {
          console.log('ssss---', error)
          setIsLoadingMatic(false)
          setTxnError(error.message)
        },
      )
      setTimeout(() => {
        handleCloseSecret()
        dispatch(sendMaticsReset())
      }, 2000)
    } else {
      setTransactionData(data)
      setAquireSecret(true)
      dispatch(sendMaticsReset())
    }
  }

  const handleCloseSecret = () => {
    setAquireSecret(false)
    setSendMatic(false)
    setDepositSelected(false)
  }

  const handleCloseExport = () => {
    setExportKeyOpted(false)
    setSendMatic(false)
    dispatch(exportKeyReset())
  }

  const handleExportKey = (evt: any) => {
    evt.preventDefault()
    setExportKeyOpted(true)
    setSendMatic(true)
  }

  return (
    <div
      className={classnames(
        'purchase-container wallet-wrapper',
        isMobile() ? 'mobile-wrapper' : '',
      )}
    >
      {/* <BottomPopup mode="wallet" isOpen={isSendMatic && !isExportKeyOpted}>
        {isSendMatic && !isExportKeyOpted && !web3CallInProgress ? (
          <Send
            onSubmit={handleFetchTransactionData}
            onClose={() => handleCloseSecret()}
          />
        ) : web3CallInProgress ? (
          <section className="new-draft vertical-flex buy-fly">
            <img
              loading="lazy"
              src={walletType === 'Metamask' ? MetamaskIcon : CoinbaseIcon}
              className="draftee-metamaskicon"
              alt="metamask-icon"
            />
            {isLoadingMatic && !txnHash && !txnError ? (
              <div className="checkout-loader-wrapper draftee-propmt">
                <div className="input-label approve-blockchain">
                  {t('please approve the blockchain transaction') +
                    ' ' +
                    walletType}
                </div>
                <div className="loading-spinner">
                  <div className="spinner__circle">
                    <div className="spinner__circle-gradient"></div>
                    <div className="spinner__circle-inner"></div>
                  </div>
                </div>
                <div className="bottom-close">
                  <div
                    className="close-button"
                    onClick={handleCloseBottomPopup}
                  >
                    {t('close')}
                  </div>
                </div>
              </div>
            ) : (
              <>
                {txnHash ? (
                  <div
                    style={{ height: '50px' }}
                    className={classnames(
                      'add-draftee-success',
                      'web3action-success',
                    )}
                  >
                    <ResponseAlert status={'Success'} />
                    {txnHash && (
                      <a
                        className="tx-link"
                        href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                        target="_blank"
                      >
                        {t('show transaction')}
                      </a>
                    )}
                  </div>
                ) : (
                  <div className="txn-err-wrapper">
                    <ResponseAlert status={'Error'} />
                    <div className="input-feedback">{txnError}</div>
                  </div>
                )}

                <div className="bottom-close">
                  <div
                    className="close-button"
                    onClick={handleCloseBottomPopup}
                  >
                    {t('close')}
                  </div>
                </div>
              </>
            )}
          </section>
        ) : null}
      </BottomPopup> */}
      {isSendMatic && !isExportKeyOpted ? (
        <SendAsset
          isOpen={isSendMatic && !isExportKeyOpted}
          onSend={handleFetchTransactionData}
          onCloseSend={handleCloseSecret}
          onClose={handleCloseBottomPopup}
          txnError={txnError}
          txnHash={txnHash}
          isLoadingMatic={isLoadingMatic}
        />
      ) : null}
      <BottomPopup
        mode="wallet"
        isOpen={isSecretAcquired || web3CallInProgress}
      >
        {isSecretAcquired && !walletType ? (
          <PassPhrase
            onClose={() => handleCloseSecret()}
            transactionData={transactionData}
          />
        ) : walletType && web3CallInProgress ? (
          <section className="new-draft vertical-flex buy-fly">
            <img
              loading="lazy"
              src={walletType === 'Metamask' ? MetamaskIcon : CoinbaseIcon}
              className="draftee-metamaskicon"
              alt="metamask-icon"
            />
            <div className="input-label approve-blockchain">
              {t('please approve the blockchain transaction') +
                ' ' +
                walletType}
            </div>
            {isLoadingMatic && !txnHash && !txnError ? (
              <div className="checkout-loader-wrapper draftee-propmt mt-40">
                {/* <div className="input-label approve-blockchain">
                  {t('please approve the blockchain transaction') +
                    ' ' +
                    walletType}
                </div> */}
                <div className="loading-spinner">
                  <div className="spinner__circle">
                    <div className="spinner__circle-gradient"></div>
                    <div className="spinner__circle-inner"></div>
                  </div>
                </div>
                <div className="bottom-close">
                  <div
                    className="close-button"
                    onClick={handleCloseBottomPopup}
                  >
                    {t('close')}
                  </div>
                </div>
              </div>
            ) : (
              <>
                {txnHash ? (
                  <div
                    style={{ height: '50px' }}
                    className={classnames(
                      'add-draftee-success',
                      'web3action-success',
                    )}
                  >
                    {/* <ResponseAlert status={'Success'} />
                    {txnHash && (
                      <a
                        className="tx-link"
                        href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                        target="_blank"
                      >
                        {t('show transaction')}
                      </a>
                    )} */}
                    <CheckCircleOutlinedIcon className="response-icon success-icon web3-success-check" />
                    <span>{t('transaction sent')}</span>
                    {txnHash && (
                      <a
                        className="tx-link button-box"
                        href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                        target="_blank"
                      >
                        {t('show transaction')}
                      </a>
                    )}
                  </div>
                ) : (
                  <div className="txn-err-wrapper">
                    {/* <ResponseAlert status={'Error'} />
                    <div className="input-feedback">{txnError}</div> */}
                    <CancelOutlinedIcon className="response-icon error-icon" />
                    <span>{t('transaction failed')}</span>
                    <div className="input-feedback">{txnError}</div>
                  </div>
                )}

                <div className="bottom-close">
                  <div
                    className="close-button"
                    onClick={handleCloseBottomPopup}
                  >
                    {t('close')}
                  </div>
                </div>
              </>
            )}
          </section>
        ) : null}
      </BottomPopup>
      <BottomPopup mode="wallet" isOpen={isDepositSelected}>
        {isDepositSelected ? (
          <Deposit
            isMaticDeposit
            title={t('my MATIC Deposit Address')}
            address={address}
            onClose={() => handleCloseSecret()}
          />
        ) : null}
      </BottomPopup>
      {isExportKeyOpted ? (
        <BottomPopup mode="wallet" isOpen={isExportKeyOpted}>
          <ExportPrivateKey onClose={() => handleCloseExport()} />
        </BottomPopup>
      ) : null}
      {loader ? (
        <div className="balance-progress">
          <div
            className={classnames(
              'loading-spinner-container mb-40 mt-40',
              'show',
            )}
          >
            <div className="loading-spinner">
              <div className="spinner__circle">
                <div className="spinner__circle-gradient"></div>
                <div className="spinner__circle-inner"></div>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <>
          <div className="balance-card">
            <h2 className="wallet-heading total-balance">
              $
              {!isNaN(USDBalance) && balance !== null
                ? '' + USDBalance
                : '0.00'}{' '}
              USD
            </h2>
            <p className="wallet-text eth-amt">
              {/* {balance} */}
              {!isNaN(balance) && balance !== null
                ? ' ' + balance
                : '0.00'}{' '}
              MATIC
            </p>
            <div className="balance-control-wrapper">
              <p onClick={handleRefreshBalance}>{t('refresh')}</p>
            </div>
          </div>
          <div className="wallet-btn-container">
            <SubmitButton
              isDisabled={false}
              title={t('buy')}
              className="wallet-btn disabled"
              onPress={() => console.log('')}
            />
            <SubmitButton
              isDisabled={false}
              title={t('send')}
              // className="wallet-btn disabled"
              className="wallet-btn"
              onPress={() => handleSendMatic()}
            />
            <SubmitButton
              isDisabled={false}
              title={t('deposit')}
              className="wallet-btn"
              onPress={() => setDepositSelected(true)}
            />
          </div>
          {!loginInfo ? (
            <>
              <div
                className="wallet-text export-key-text"
                onClick={handleExportKey}
              >
                {' '}
                <IosShareIcon />
                <span>{t('export private key')}</span>
              </div>
              <p className="wallet-text form-note save-pk">
                {t('IMPORTANT')}:{' '}
                {t('you must store this private key somewhere')}
              </p>
            </>
          ) : null}
        </>
      )}
    </div>
  )
}

export default MyWallet
